    // 压入历史栈
    pushState (query) {
      // let _this = this
      let popStateData = {}

      popStateData.userInfo = this.userInfo
      popStateData.favoriteExpertList = this.favoriteExpertList
      popStateData.expertQueryList = this.expertQueryList
      popStateData.queryCondition = this.queryCondition
      popStateData.expertQueryListPageTotal = this.expertQueryListPageTotal
      popStateData.expertQueryListPageCurrent = this.expertQueryListPageCurrent
      console.log(popStateData)

      if (location.href.indexOf('#') !== -1) {
        console.log('压入历史栈: ' + location.href.split('#')[0] + '#/' + query)
        history.pushState(popStateData, document.title, location.href.split('#')[0] + '#/' + query)
      } else {
        console.log('压入历史栈: ' + location.href)
        history.pushState(popStateData, document.title, location.href)
      }
    },
    // 从路由获取数据，并压栈
    loadUserCenterPage () {
      'use strict'
      // console.log('loadUserCenterPage ()')
      let _this = this

      let serverUrl = 'user_center'
      let data = {}

      // HTTP Request (JQuery Ajax)
      $.ajax({
        type: 'GET',
        url: serverUrl,
        data: data,
        dataType: 'json',
        beforeSend: function (xhr) {
        },
        success: function (result, status, xhr) {
          console.log(result)
          if (!result || result === '' || result.length === 0) {
            alert('抱歉，无用户信息，无法跳转用户中心。')
            console.log('抱歉，无用户信息，无法跳转用户中心。')
            return false
          } else {
            // 解析JSON数据
            let loginResult = result.loginResult

            // 验证从后端返回的登录结果标志
            console.log('loginResult === ' + loginResult.toString())
            if (loginResult !== 'success') {
              console.log('抱歉，登录验证失败，无法跳转用户中心。')
              alert('抱歉，登录验证失败，无法跳转用户中心。')
              return false
            } else {
              let flag = true

              let userInfo
              if ('myuser' in result) {
                userInfo = JSON.parse(result.myuser)
              } else {
                flag = false
              }
              let favoriteExpertList = []

              if ('favExperts' in result) {
                let favoriteExpertListTemp = JSON.parse(result.favExperts)
                favoriteExpertListTemp.forEach(function (item, index) {
                  favoriteExpertList.push(JSON.parse(item))
                })
              } else {
                flag = false
              }

              // 如果没有应有的数据，则不会跳转到用户中心页面
              let testData = userInfo
              if (JSON.stringify(testData) !== '{}') {
                if (testData !== undefined) {
                  if ('id' in testData) {
                    if (testData.id === undefined) {
                      flag = false
                    }
                  } else {
                    flag = false
                  }
                } else {
                  flag = false
                }
              } else {
                flag = false
              }

              if (flag) {
                console.log('登录验证成功！跳转用户中心。')

                // 获得数据
                _this.userInfo = userInfo
                _this.favoriteExpertList = favoriteExpertList

                // 压入历史栈
                _this.pushState('user_center/' + _this.userInfo.id)

                // 渲染用户关注的专家
                _this.renderFavoriteExpert()
                // 渲染用户信息框
                _this.renderUserInfo()
              } else {
                console.log('抱歉，无用户信息，无法跳转用户中心。')
                alert('抱歉，无用户信息，无法跳转用户中心。')
                window.location.href = '/'
                return false
              }
            }
          }
        },
        error: function (xhr, status, error) {
          alert('HTTP GET Error! status=' + status + '&error=' + error +
            '&statusCode=' + xhr.status + '&responseText=' + xhr.responseText +
            '&readyState=' + xhr.readyState)
        },
        statusCode: {
          // 当响应对应的状态码时，执行对应的回调函数
          200: function () {
            console.log('200: 请求成功')
          },
          404: function () {
            console.log('404: 找不到页面')
            alert('404: 找不到页面')
          },
          500: function () {
            console.log('500: 服务器错误')
            alert('500: 服务器错误')
          }
        }
      })
    },
