    // 导航栏查询输入控制 TODO 目前仅为输入专家姓名的普通查询
    queryFunction () {
      'use strict'
      let _this = this
      let queryInput = $('input.navigation-input').val().trim()
      let queryType = 'normal'
      let querySelection = 'researcher'
      console.log('queryFunction() query-input=' + queryInput +
