    },
    // 分页展示查询结果-上一页
    previousQueryResultPage () {
      'use strict'
      let _this = this

      let jqCurrentPage = $('#query-result-current-page')
      let jqQueryResultPreviousPage = $('#query-result-previous-page')
      let jqQueryResultNextPage = $('#query-result-next-page')

      if (!this.expertQueryListPageCurrent || this.expertQueryListPageCurrent <= 0 ||
        this.expertQueryListPageCurrent > this.expertQueryListPageTotal) {
        console.log('result-page-error! expertQueryListPageCurrent is out of range.')
        return false
      } else if (this.expertQueryListPageCurrent === 1) {
        alert('当前页已经是第一页了！')
        return false
      } else {
        this.expertQueryListPageCurrent--

        jqCurrentPage.empty().append(this.expertQueryListPageCurrent)

        // 若按上一页之后到了第一页,则使上一页按钮不可选
        if (this.expertQueryListPageCurrent === 1 && !jqQueryResultPreviousPage.hasClass('disabled')) {
          jqQueryResultPreviousPage.addClass('disabled')
        }

        // 若下一页按钮之前有disabled属性,则撤销之
        if (jqQueryResultNextPage.hasClass('disabled')) {
          jqQueryResultNextPage.removeClass('disabled')
        }

        // 改变页面中的论文内容
        _this.changeQueryResultPage(this.expertQueryListPageCurrent)
      }
      return true
    },
    // 分页展示查询结果-下一页
    nextQueryResultPage () {
      'use strict'
      let _this = this

      let jqCurrentPage = $('#query-result-current-page')
      let jqQueryResultPreviousPage = $('#query-result-previous-page')
      let jqQueryResultNextPage = $('#query-result-next-page')

      if (!this.expertQueryListPageCurrent || this.expertQueryListPageCurrent <= 0 ||
        this.expertQueryListPageCurrent > this.expertQueryListPageTotal) {
        console.log('next-page-error! expertQueryListPageCurrent is out of range.')
        return false
      } else if (this.expertQueryListPageCurrent === this.expertQueryListPageTotal) {
        alert('当前页已经是最后一页了！')
        return false
      } else {
        this.expertQueryListPageCurrent++

        jqCurrentPage.empty().append(this.expertQueryListPageCurrent)

        // 若按下一页之后到了最后一页,则使下一页按钮不可选
        if (this.expertQueryListPageCurrent === this.expertQueryListPageTotal &&
          !jqQueryResultNextPage.hasClass('disabled')) {
          jqQueryResultNextPage.addClass('disabled')
        }

        // 若上一页按钮之前有disabled属性,则撤销之
        if (jqQueryResultPreviousPage.hasClass('disabled')) {
          jqQueryResultPreviousPage.removeClass('disabled')
        }

        // 改变页面中的论文内容
        _this.changeQueryResultPage(this.expertQueryListPageCurrent)
      }
      return true
    },
    // 分页展示查询结果-按页码跳转
    jumpQueryResultPage () {
      'use strict'
      let _this = this

      let jqCurrentPage = $('#query-result-current-page')
      let jqQueryResultPreviousPage = $('#query-result-previous-page')
      let jqQueryResultNextPage = $('#query-result-next-page')
      let pageInput = Math.floor(parseInt($('#query-result-jump-input').val().toString().trim(), 10))

      if (!this.expertQueryListPageCurrent || this.expertQueryListPageCurrent <= 0 ||
        this.expertQueryListPageCurrent > this.expertQueryListPageTotal) {
        console.log('input-jump-page-error! expertQueryListPageCurrent is out of range.')
        return false
      } else if (!pageInput || pageInput === '') {
        alert('请输入合法页码！')
        pageInput.focus()
        return false
      } else if (parseInt(pageInput, 10) <= 0 || parseInt(pageInput, 10) > this.expertQueryListPageTotal) {
        alert('论文信息总共' + this.expertQueryListPageTotal + '页，请输入该范围内的页码！')
        pageInput.focus()
        return false
      } else {
        this.expertQueryListPageCurrent = parseInt(pageInput, 10)

        jqCurrentPage.empty().append(this.expertQueryListPageCurrent)

        // 若跳转到了第一页,则使上一页按钮不可选
        if (this.expertQueryListPageCurrent === 1 && !jqQueryResultPreviousPage.hasClass('disabled')) {
          jqQueryResultPreviousPage.addClass('disabled')
        }

        // 若跳转到了最后一页,则使下一页按钮不可选
        if (this.expertQueryListPageCurrent === this.expertQueryListPageTotal &&
          !jqQueryResultNextPage.hasClass('disabled')) {
          jqQueryResultNextPage.addClass('disabled')
        }

        // 若跳转到了中间页码,使上一页和下一页按钮都可选
        if (this.expertQueryListPageCurrent > 1 && this.expertQueryListPageCurrent < this.expertQueryListPageTotal) {
          if (jqQueryResultPreviousPage.hasClass('disabled')) {
            jqQueryResultPreviousPage.removeClass('disabled')
          }
          if (jqQueryResultNextPage.hasClass('disabled')) {
            jqQueryResultNextPage.removeClass('disabled')
          }
        }

        // 改变页面中的论文内容
        _this.changeQueryResultPage(this.expertQueryListPageCurrent)
      }

      return true
    },
    // 换页时，改变查询结果内容
    changeQueryResultPage (currentPage) {
      'use strict'
      let _this = this

      _this.queryListAjax(_this.queryCondition, currentPage)

      // 压入历史栈
      _this.pushState('user_center/' + _this.userInfo.id)
    },
    // 展示用户信息
    showInfo () {
      $('#editMess').css({'display': 'block'})
      $('#favorite-expert-article').css({'display': 'none'})
    },
    // 展示关注学者
    showExpert () {
      $('#editMess').css({'display': 'none'})
      $('#favorite-expert-article').css({'display': 'block'})
    },
    // 修改个人信息
    editInfo () {
      let _this = this
      let data = {}
      let serverUrl = 'edit_info'

      data.username = $('input#username').val()
      data.company = $('input#company').val()
      data.gender = $('select#gender option:selected').val()
      data.interests = $('input#interests').val()
      console.log(data)

      // HTTP Request (JQuery Ajax)
      $.ajax({
        type: 'GET',
        url: serverUrl,
        data: data,
        dataType: 'json',
        beforeSend: function (xhr) {
        },
        success: function (result, status, xhr) {
          console.log(result)
          if (!result || result === '' || result.length === 0) {
            alert('抱歉，修改个人信息失败！')
            console.log('抱歉，未能成功修改您的用户信息！')
            return false
          } else {
            // 解析JSON数据
            let userInfo = JSON.parse(result.myuser)
            // let favoriteExpertList = []

            // let favoriteExpertListTemp = JSON.parse(result.favExperts)
            // favoriteExpertListTemp.forEach(function (item, index) {
            //   favoriteExpertList.push(JSON.parse(item))
            // })

            let flag = true

            // 如果没有应有的数据，则不会跳转到用户中心页面
            let testData = userInfo
            if (JSON.stringify(testData) !== '{}') {
              if (testData !== undefined) {
                if ('id' in testData) {
                  if (testData.id === undefined) {
                    flag = false
                  }
                } else {
                  flag = false
                }
              } else {
                flag = false
              }
            } else {
              flag = false
            }

            if (flag) {
              alert('修改个人信息成功！')
              console.log('修改个人信息成功！')

              _this.userInfo = userInfo

              // 压入历史栈
              _this.pushState('user_center/' + _this.userInfo.id)

              // 渲染个人信息输入框
              _this.renderUserInfo()
            } else {
              alert('抱歉，修改个人信息失败！')
              console.log('抱歉，修改个人信息失败，您的用户信息不完整！')
              return false
            }
          }
        },
        error: function (xhr, status, error) {
          alert('HTTP GET Error! status=' + status + '&error=' + error +
            '&statusCode=' + xhr.status + '&responseText=' + xhr.responseText +
            '&readyState=' + xhr.readyState)
        },
        statusCode: {
          // 当响应对应的状态码时，执行对应的回调函数
          200: function () {
            console.log('200: 请求成功')
          },
          404: function () {
            console.log('404: 找不到页面')
            alert('404: 找不到页面')
          },
          500: function () {
            console.log('500: 服务器错误')
            alert('500: 服务器错误')
          }
        }
      })
    },
    // 修改密码
    changePassword () {
      // let _this = this
      let data = {}
      let serverUrl = 'change_pwd'

      data.pwd1 = $('input#pwd1').val()
      data.pwd2 = $('input#pwd2').val()
      console.log(data)

      // HTTP Request (JQuery Ajax)
      $.ajax({
        type: 'GET',
        url: serverUrl,
        data: data,
        dataType: 'json',
        beforeSend: function (xhr) {
        },
        success: function (result, status, xhr) {
          console.log(result)
          if (!result || result === '' || result.length === 0) {
            alert('抱歉，修改密码失败！')
            console.log('抱歉，未能成功修改您的密码！')
            return false
          } else {
            // 解析JSON数据，根据后端返回值来做出响应
            let changePwdResult = result.changePwdResult

            if (changePwdResult !== undefined) {
              if (changePwdResult === '修改成功') {
                alert('修改密码成功！')
                console.log('修改密码成功！')
              } else if (changePwdResult === '两次密码不一致') {
                alert('抱歉，您输入的两两次密码不一致，修改密码失败！')
                console.log('抱歉，您输入的两两次密码不一致，修改密码失败！')
              } else {
                alert('抱歉，修改密码失败！')
                console.log('异常，修改密码失败！')
              }
            } else {
              alert('抱歉，修改密码失败！')
              console.log('异常，修改密码失败！')
            }
          }
        },
        error: function (xhr, status, error) {
          alert('HTTP GET Error! status=' + status + '&error=' + error +
            '&statusCode=' + xhr.status + '&responseText=' + xhr.responseText +
            '&readyState=' + xhr.readyState)
        },
        statusCode: {
          // 当响应对应的状态码时，执行对应的回调函数
          200: function () {
            console.log('200: 请求成功')
          },
          404: function () {
            console.log('404: 找不到页面')
            alert('404: 找不到页面')
          },
          500: function () {
            console.log('500: 服务器错误')
            alert('500: 服务器错误')
          }
        }
      })
    },
    // 退出登录
    logout () {
      let _this = this

      if (confirm('确认注销')) {
        let serverUrl = 'logout'
        let data = {}

        // HTTP Request (JQuery Ajax)
        $.ajax({
          type: 'GET',
          url: serverUrl,
          data: data,
          dataType: 'json',
          beforeSend: function (xhr) {
          },
          success: function (result, status, xhr) {
            console.log(result)
            if (!result || result === '' || result.length === 0) {
              alert('抱歉，登录失败！')
              console.log('HTTP result 异常！')
              return false
            } else {
              // 解析JSON数据
              let logoutResult = result.logoutResult

              // 验证从后端返回的登录结果标志
              console.log('logoutResult === ' + logoutResult.toString())
              if (logoutResult === 'success') {
                console.log('注销成功！')

                // 删除 sessionStorage 的 userId
                sessionStorage.removeItem('user_id')

                // 修改登录状态
                if (_this.$store.state.loginFlag) {
                  _this.$store.dispatch('changeLoginFlag', false)
                }

                // 跳转回到主页
                window.location.href = '/'

                // alert('注销成功！')
              } else if (logoutResult === 'fail') {
                console.log('抱歉，注销失败！')
                alert('抱歉，注销失败！')
              } else {
                console.log('抱歉，注销失败！')
                alert('异常，注销失败！')
              }
            }
          },
          error: function (xhr, status, error) {
            alert('HTTP GET Error! status=' + status + '&error=' + error +
              '&statusCode=' + xhr.status + '&responseText=' + xhr.responseText +
              '&readyState=' + xhr.readyState)
          },
          statusCode: {
            // 当响应对应的状态码时，执行对应的回调函数
            200: function () {
              console.log('200: 请求成功')
            },
            404: function () {
              console.log('404: 找不到页面')
              alert('404: 找不到页面')
            },
            500: function () {
              console.log('500: 服务器错误')
              alert('500: 服务器错误')
            }
          }
        })
      }
    },
    // 查询专家详情
    queryExpertDetailAjax (expertId) {
      // 切换路由
      let query = '/expert_detail/' + expertId
      this.$router.push(query)
    },
    // 平滑移动到锚点
    scrollAnchor (anchorId) {
      $('html, body').animate({
        scrollTop: $('#' + anchorId).offset().top - 3 * 16
      }, 600)
    }
  }
}
</script>

<style>
  button.navigation-search {
    box-shadow: none;
  }

  div#user-center article.main-module div.ccn-selection-list ul {
    margin: 0;
    padding: 2.5em 3em 1.5em;
  }

  div.user-center-container {
    margin-top: 4em;
    margin-bottom: 1em;
  }

  div.user-center-starter {
    padding: 15px;
  }

  /*article#favorite-expert-article {*/
    /*display: none;*/
  /*}*/

  #favorite-expert-pages > ul {
    margin: 0;
    padding: 2.5em 3em 1.5em;
  }

  #favorite-expert-pages > ul li.show-query-result {
    display: table-cell;
    padding: 0.875em 0 0 1em;
    text-align: center;
    vertical-align: middle;
  }

  #favorite-expert-pages > ul li.show-query-result p{
    display: inline-block;
    text-align: center;
  }

  #favorite-expert-pages ul li:nth-child(4) {
    padding: 0.43em 0 0 1em;
  }

  #favorite-expert-jump-input {
    text-align: center;
  }

  #favorite-expert-list-div {
    width: 95%;
    margin: auto;
    /*max-height: 60%;*/
    overflow-y: scroll;
  }

  div#user-center .favorite-expert-list dl.expert-profile {
    width: 18.8%;
    height: 190px;
    padding: 0.2%;
    margin: 1% 0.5% 1% 0.5%;
    float: left;
    position: relative;
    overflow: hidden;
    border: 1px solid #dedede;
  }

  div#user-center .favorite-expert-list dl.expert-profile dt {
    display: inline-block;
    padding: 0;
    margin: 0.5% 0 0.5% 0;
  }

  div#user-center .favorite-expert-list dl.expert-profile dd {
    padding: 0;
    margin: 0.2% 0 0.2% 0;
  }

  div#user-center .favorite-expert-list dl.expert-profile dd > a {
    cursor: pointer;
    padding: 0;
    margin: 0;
    text-decoration: none;
    font-size: 14px;
    font-weight: bold;
  }

  div#user-center .favorite-expert-list dl.expert-profile dd > p {
    display: block;
    padding: 0;
    margin: 0;
    width: auto;
    height: auto;
    overflow: hidden;
    font-size: 12px;
    font-style: normal;
    color: #232323;
  }

  div#user-center .favorite-expert-list dl.expert-profile dt.dt-img img {
    width: 50px;
    height: 50px;
    cursor: pointer;
  }
</style>



