<template>
  <dl class="expert-profile">
    <dt class="dt-img">
      <img :src="imgUrl" :data-id="id" alt="researcher image"/>
    </dt>
    <dd><p>{{ name }}</p></dd>
    <dd><p><strong>影响力：{{ influenceScore }}</strong></p></dd>
    <dd><p><strong>学校</strong>：</p>{{ university }}</dd>
    <dd><p><strong>学院</strong>：{{college }}</p></dd>
    <dd><p><strong>研究方向</strong>：{{themeList }}</p></dd>
  </dl>
</template>

<script>
import $ from 'jquery'

export default {
  name: 'ExpertProfile',
  data () {
    return {
      imgUrl: '',
      id: '',
      name: '',
      influenceScore: 0,
      university: '',
      college: '',
      themeList: ''
    }
  },
  // 组件生命周期钩子
  beforeCreate () {
    console.log('ExpertProfile.vue beforeCreate 创建前状态 ===============>')
  },
  created () {
    console.log('ExpertProfile.vue created 创建完毕状态 ===============>')
  },
  beforeMount () {
    console.log('ExpertProfile.vue beforeMount 挂载前状态 ===============>')
  },
  mounted () {
    console.log('ExpertProfile.vue mounted 挂载完毕状态 ===============>')

    // Vue 数据初始化
    this.imgUrl = '../../static/asset/image/default_expert_image.png'
    this.id = ''
    this.name = '无。'
    this.influenceScore = 0
    this.university = '无。'
    this.college = '无。'
    this.themeList = '无。'

    // 组件初始化
    this.init()
  },
  beforeUpdate () {
    console.log('ExpertProfile.vue beforeUpdate 更新前状态 ===============>')
  },
  updated () {
    console.log('ExpertProfile.vue updated 更新完毕状态 ===============>')
  },
  beforeDestroy () {
    console.log('ExpertProfile.vue beforeDestroy 销毁前状态 ===============>')
  },
  destroyed () {
    console.log('ExpertProfile.vue destroyed 销毁完毕状态 ===============>')
  },
  props: {
    result: {
      type: Object,
      require: true
    }
  },
  methods: {
    init () {
      console.log('ExpertProfile.vue init ()')
      let _this = this

      // 根据父组件的值修改 data
      this.id = this.result.id
      this.name = this.result.name
      this.influenceScore = parseFloat(this.result.score).toFixed(0)
      this.university = this.result.university
      this.college = this.result.college

      if (!this.result.img_url || this.result.img_url === '' || this.result.img_url === null) {
        if (!this.result.theme_list || this.result.theme_list === '' || this.result.theme_list === null) {
          // 无头像，无简介
          this.imgUrl = '../../static/asset/image/default_expert_image.png'
          this.themeList = '无。'
        } else {
          // 无头像，有简介
          this.imgUrl = '../../static/asset/image/default_expert_image.png'
          this.themeList = this.result.theme_list
        }
      } else {
        if (!this.result.theme_list || this.result.theme_list === '' || this.result.theme_list === null) {
          // 有头像，无简介
          this.imgUrl = this.result.img_url
          this.themeList = '无。'
        } else {
          // 有头像，有简介
          this.imgUrl = this.result.img_url
          this.themeList = this.result.theme_list
        }
      }

      console.log(this.imgUrl)
      console.log(this.id)
      console.log(this.name)
      console.log(this.influenceScore)
      console.log(this.university)
      console.log(this.college)
      console.log(this.themeList)

      // 增添点击事件
      $('dt.dt-img img').click(function () {
        _this.queryDetailAjax($(this).attr('data-id'))
      })
    },
    // 查询专家详情
    queryDetailAjax (expertId) {
      'use strict'

      // 切换路由
      this.$router.replace('/expert_detail/' + expertId)
    }
  }
}
</script>

<style scoped>
  dl.expert-profile {
    width: 350px;
    height: 190px;
    padding: 2px;
    margin: 10px 5px 10px 5px;
    float: left;
    position: relative;
    overflow: scroll;
    border: 1px solid #dedede;
  }
  dl.expert-profile dt {
    display: inline-block;
    padding: 0;
    margin: 5px 0 5px 0;
  }
  dl.expert-profile dd {
    padding: 0;
    margin: 2px 0 2px 0;
  }
  dl.expert-profile dd > a {
    cursor: pointer;
    padding: 0;
    margin: 0;
    text-decoration: none;
    font-size: 14px;
    font-weight: bold;
  }
  dl.expert-profile dd > p {
    display: block;
    padding: 0;
    margin: 0;
    width: auto;
    height: auto;
    overflow: hidden;
    font-size: 12px;
    font-style: normal;
    color: #232323;
  }
  dl.expert-profile dt.dt-img img {
    width: 50px;
    height: 50px;
    cursor: pointer;
  }
</style>



