import Vue from 'vue'
import Router from 'vue-router'
import Index from '../components/Index'
import Detail from '../components/Detail'
import UserCenter from '../components/UserCenter'
import PaperDetail from '../components/PaperDetail'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    // {
    //   path: '/index/:queryUrl',
    //   name: 'Index',
    //   component: Index
    // },
    {
      path: '/expert_detail/:expert_id',
      name: 'Detail',
      component: Detail
    },
    {
      path: '/user_center/:user_id',
      name: 'UserCenter',
      component: UserCenter
    },
    {
      path: '/paper_detail/:paper_id',
      name: 'PaperDetail',
      component: PaperDetail
    }
  ]
})

// 全局路由守卫
router.beforeEach((to, from, next) => {
  // to: Route: 即将要进入的目标 路由对象
  // from: Route: 当前导航正要离开的路由
  // next: Function: 一定要调用该方法来 resolve 这个钩子。执行效果依赖 next 方法的调用参数。
  // console.log('navigation-guards')
  // console.log(to)
  // console.log(from)
  // console.log(next)

  // 登录检查
  // const route = ['home', 'list']
  // let isLogin = isLogin // 是否登录
  // // 未登录状态；当路由到route指定页时，跳转至 login
  // if (route.indexOf(to.name) >= 0) {
  //   if (!isLogin) {
  //     this.$router.push({path: '/login'})
  //   }
  // }
  // // 已登录状态；当路由到 login 时，跳转至home
  // if (to.name === 'login') {
  //   if (isLogin) {
  //     this.$router.push({ path: '/home'})
  //   }
  // }

  // 禁止页面回退
  // let allowBack = true // 默认值 true
  // if (to.meta.allowBack !== undefined) {
  //   allowBack = to.meta.allowBack
  // }
  // if (!allowBack) {
  //   history.pushState(null, null, location.href)
  // }
  // // 更新 vuex 的 allowBack 的值
  // store.dispatch('updateAppSetting', {
  //   allowBack: allowBack
  // })
  // window.history.pushState(null, null, window.location.href)

  next()
})

export default router



