<template>
  <div>
    <expert-profile v-for="expert in ccnCurrentExpert" :key="expert.id" :result="expert"></expert-profile>
  </div>
</template>

<script>
import $ from 'jquery'
import ExpertProfile from './ExpertProfile'

export default {
  name: 'HottestExpert',
  components: {
    'expert-profile': ExpertProfile
  },
  data () {
    return {
    }
  },
  // 获取父组件的传值
  props: {
    result: {
      type: Object,
      require: true
    },
    activeHottestExpertClass: {
      type: String,
      require: true
    }
  },
  computed: {
    // 获取当前选中类别的热门专家列表
    ccnCurrentExpert () {
      console.log('HottestExpert.vue ccnCurrentExpert ()')
      console.log(this.result)
      console.log(this.activeHottestExpertClass)

      if ((typeof this.result) === 'undefined' || (typeof this.activeHottestExpertClass) === 'undefined') {
        return []
      }

      let _result = []
      let _class = this.activeHottestExpertClass
      this.result.ccn_experts.forEach(function (ccnExpertsItem) {
        if (_class === ccnExpertsItem.ccn_number) {
          ccnExpertsItem.experts.forEach(function (expertItem) {
            let _info = {}
            _info.img_url = expertItem.img_url
            _info.id = expertItem.id
            _info.name = expertItem.name
            _info.university = expertItem.university
            _info.college = expertItem.college
            _info.theme_list = expertItem.theme_list
            _info.score = expertItem.score
            _result.push(_info)
          })
        }
      })
      return _result
    }
  },
  // 组件生命周期钩子
  beforeCreate () {
    console.log('HottestExpert.vue beforeCreate 创建前状态 ===============>')
  },
  created () {
    console.log('HottestExpert.vue created 创建完毕状态 ===============>')
  },
  beforeMount () {
    console.log('HottestExpert.vue beforeMount 挂载前状态 ===============>')
  },
  mounted () {
    console.log('HottestExpert.vue mounted 挂载完毕状态 ===============>')

    // 组件初始化
    this.init()
  },
  beforeUpdate () {
    console.log('HottestExpert.vue beforeUpdate 更新前状态 ===============>')
  },
  updated () {
    console.log('HottestExpert.vue updated 更新完毕状态 ===============>')
  },
  beforeDestroy () {
    console.log('HottestExpert.vue beforeDestroy 销毁前状态 ===============>')
  },
  destroyed () {
    console.log('HottestExpert.vue destroyed 销毁完毕状态 ===============>')
  },
  methods: {
    init () {
      console.log('HottestExpert.vue init ()')
      console.log(this.result)
      console.log(this.activeHottestExpertClass)
      // console.log(this.ccnCurrentExpert)

      // console.log('HottestExpert.vue ccnCurrentExpert ()')
      // console.log(this.result)
      // console.log(this.activeHottestExpertClass)

      if ((typeof this.result) === 'undefined' || (typeof this.activeHottestExpertClass) === 'undefined') {
        this.ccnCurrentExpert = []
        return
      }

      let _result = []
      let _class = this.activeHottestExpertClass
      this.result.ccn_experts.forEach(function (ccnExpertsItem) {
        if (_class === ccnExpertsItem.ccn_number) {
          ccnExpertsItem.experts.forEach(function (expertItem) {
            let _info = {}
            _info.img_url = expertItem.img_url
            _info.id = expertItem.id
            _info.name = expertItem.name
            _info.university = expertItem.university
            _info.college = expertItem.college
            _info.theme_list = expertItem.theme_list
            _info.score = expertItem.score
            _result.push(_info)
          })
        }
      })
      this.ccnCurrentExpert = _result
    },
    // 平滑移动到锚点
    scrollAnchor (anchorId) {
      $('html, body').animate({
        // 该div容器的padding-top是3em
        scrollTop: $('#' + anchorId).offset().top - 3 * 16
      }, 600)
      // document.getElementById(anchor_id).scrollIntoView()
    }
  }
}
</script>

<style scoped>
  article.main-module div.query-selection-list ul {
    margin: 0;
    padding: 2.5em 3em 1.5em;
  }

  article.main-module div.ccn-selection-list ul {
    margin: 0;
    padding: 2.5em 3em 1.5em;
  }

  article.main-module {
    padding: 3em 3em 1em 3em ;
    background: #fff;
    border: solid 1px rgba(160, 160, 160, 0.3);
    margin: 0 1em 3em 1em;
    position: relative;
    overflow: scroll;
  }

  article.main-module > header {
    display: -moz-flex;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    border-bottom: solid 1px rgba(160, 160, 160, 0.3);
    left: -3em;
    margin: -3em 0 1em 0;
    position: relative;
    width: calc(100% + 6em);
  }

  article.main-module > header .title {
    -moz-flex-grow: 1;
    -webkit-flex-grow: 1;
    flex-grow: 1;
    -ms-flex: 1;
    padding: 2.5em 3em 1.5em 3em;
  }

  article.main-module > header .title h2 {
    font-weight: 900;
    font-size: 1.5em;
  }

  article.main-module > header .title h2 a {
    cursor: pointer;
  }

  article.main-module > header .title > :last-child {
    margin-bottom: 0;
  }

  article.main-module > header .meta {
    padding: 0.5em 3em 0.5em 3em;
    border-left: solid 1px rgba(160, 160, 160, 0.3);
    min-width: 17em;
    text-align: right;
    width: fit-content;
  }

  article.main-module > header .meta > * {
    margin: 0;
  }

  article.main-module > header .meta span {
    display: flex;
  }

  article.main-module > header .meta span div {
    margin: 0;
  }

  article.main-module > header .meta span div.p {
    color: #3c3b3b;
    display: block;
    text-align: left;
    font-family: "Raleway", Helvetica, sans-serif;
    font-size: 0.7em;
    font-weight: 800;
    margin: 0.1em 0 0.1em 0 !important;
    padding: 0 !important;
    text-transform: uppercase;
    white-space: nowrap;
  }

  article.main-module > a.image.featured {
    overflow: hidden;
  }

  article.main-module > a.image.featured img {
    -moz-transition: -moz-transform 0.2s ease-out;
    -webkit-transition: -webkit-transform 0.2s ease-out;
    -ms-transition: -ms-transform 0.2s ease-out;
    transition: transform 0.2s ease-out;
  }

  article.main-module > a.image.featured:hover img {
    -moz-transform: scale(1.05);
    -webkit-transform: scale(1.05);
    -ms-transform: scale(1.05);
    transform: scale(1.05);
  }

  article.main-module > footer {
    display: -moz-flex;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    -moz-align-items: center;
    -webkit-align-items: center;
    -ms-align-items: center;
    align-items: center;
  }

  article.main-module > footer .actions {
    -moz-flex-grow: 1;
    -webkit-flex-grow: 1;
    -ms-flex-grow: 1;
    flex-grow: 1;
  }

  @media screen and (max-width: 980px) {

    article.main-module {
      border-left: 0;
      border-right: 0;
      left: -3em;
      width: calc(100% + (3em * 2));
    }

    article.main-module > header {
      -moz-flex-direction: column;
      -webkit-flex-direction: column;
      -ms-flex-direction: column;
      flex-direction: column;
      padding: 3.75em 3em 1.25em 3em ;
      border-left: 0;
    }

    article.main-module > header .title {
      -ms-flex: 0 1 auto;
      margin: 0 0 2em 0;
      padding: 0;
      text-align: center;
    }

    article.main-module > header .meta {
      -moz-align-items: center;
      -webkit-align-items: center;
      -ms-align-items: center;
      align-items: center;
      display: -moz-flex;
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      -moz-justify-content: center;
      -webkit-justify-content: center;
      -ms-justify-content: center;
      justify-content: center;
      border-left: 0;
      margin: 0 0 2em 0;
      padding: 0;
      text-align: left;
      width: 100%;
    }

    article.main-module > header .meta > * {
      border-left: solid 1px rgba(160, 160, 160, 0.3);
      margin-left: 2em;
      padding-left: 2em;
    }

    article.main-module > header .meta > :first-child {
      border-left: 0;
      margin-left: 0;
      padding-left: 0;
    }

    article.main-module > header .meta .p {
      margin-bottom: 0;
      margin-top: 0;
    }

    article.main-module > header .meta .author {
      -moz-flex-direction: row-reverse;
      -webkit-flex-direction: row-reverse;
      -ms-flex-direction: row-reverse;
      flex-direction: row-reverse;
      margin-bottom: 0;
    }

    article.main-module > header .meta .author .name {
      margin: 0 0 0 1.5em;
    }

    article.main-module > header .meta .author img {
      width: 3.5em;
    }

  }

  @media screen and (max-width: 736px) {

    article.main-module {
      padding: 1.5em 1.5em 0.1em 1.5em ;
      left: -1.5em;
      margin: 0 0 2em 0;
      width: calc(100% + (1.5em * 2));
    }

    article.main-module > header {
      padding: 3em 1.5em 0.5em 1.5em ;
      left: -1.5em;
      margin: -1.5em 0 1.5em 0;
      width: calc(100% + 3em);
    }

    article.main-module > header .title h2 {
      font-size: 1.1em;
    }

  }

  @media screen and (max-width: 480px) {

    article.main-module > header .meta {
      -moz-align-items: center;
      -webkit-align-items: center;
      -ms-align-items: center;
      align-items: center;
      -moz-flex-direction: column;
      -webkit-flex-direction: column;
      -ms-flex-direction: column;
      flex-direction: column;
    }

    article.main-module > header .meta > * {
      border-left: 0;
      margin: 1em 0 0 0;
      padding-left: 0;
    }

    article.main-module > header .meta .author .name {
      display: none;
    }

    article.main-module > .image.featured {
      margin-left: -1.5em;
      margin-top: calc(-1.5em - 1px);
      width: calc(100% + 3em);
    }

    article.main-module > footer {
      -moz-align-items: -moz-stretch;
      -webkit-align-items: stretch;
      -ms-align-items: -ms-stretch;
      align-items: stretch;
      -moz-flex-direction: column-reverse;
      -webkit-flex-direction: column-reverse;
      -ms-flex-direction: column-reverse;
      flex-direction: column-reverse;
    }

  }
</style>



