// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Vuex from 'vuex'
import axios from 'axios'
import qs from 'qs'

// 引入全局样式
import '../static/sass/main.scss'

// 注册 vuex
Vue.use(Vuex)

// 把 axios 和 qs 写入 Vue 的原型
Vue.prototype.axios = axios
Vue.prototype.qs = qs

let store = new Vuex.Store({
  // 定义初始数据
  state: {
    loginFlag: false,
    indexPageData: {},
    expertQueryList: [],
    expertQueryListPageTotal: 0,
    expertQueryListPageCurrent: 0,
    detailPageData: {},
    paperPageTotal: 0,
    paperPageCurrent: 0
  },
  getters: {
    // 可以通过一个过滤器，来获取某个 state 的某个值
    ccnExperts: state => {
      return state.indexPageData.filter(item => item.ccn_experts)
    }
  },
  // 同步方法，触发同步里面的方法是用 this.$store.commit('方法名')
  mutations: {
    // 改变 loginFlag 的值
    changeLoginFlagMutation (state, newLoginFlag) {
      console.log('$store changeLoginFlagMutation', newLoginFlag)
      state.loginFlag = newLoginFlag
    },
    changeIndexPageDataMutation (state, newIndexPageData) {
      console.log('$store changeIndexPageDataMutation', newIndexPageData)
      state.indexPageData = newIndexPageData
    },
    changeExpertQueryListMutation (state, newExpertQueryList) {
      console.log('$store changeExpertQueryListMutation', newExpertQueryList)
      state.expertQueryList = newExpertQueryList
    },
    changeExpertQueryListPageTotalMutation (state, newExpertQueryListPageTotal) {
      console.log('$store changeExpertQueryListPageTotalMutation', newExpertQueryListPageTotal)
      state.expertQueryListPageTotal = newExpertQueryListPageTotal
    },
    changeExpertQueryListPageCurrentMutation (state, newExpertQueryListPageCurrent) {
      console.log('$store changeExpertQueryListPageCurrentMutation', newExpertQueryListPageCurrent)
      state.expertQueryListPageCurrent = newExpertQueryListPageCurrent
    },
    changeDetailPageDataMutation (state, newDetailPageData) {
      console.log('$store changeDetailPageDataMutation', newDetailPageData)
      state.detailPageData = newDetailPageData
    },
    changePaperPageTotalMutation (state, newPaperPageTotal) {
      console.log('$store changePaperPageTotalMutation', newPaperPageTotal)
      state.paperPageTotal = newPaperPageTotal
    },
    changePaperPageCurrentMutation (state, newPaperPageCurrent) {
      console.log('$store changePaperPageCurrentMutation', newPaperPageCurrent)
      state.paperPageCurrent = newPaperPageCurrent
    }
  },
  // 异步方法，触发异步里面的方法是用 this.$store.dispatch('方法名')
  actions: {
    // 修改登录状态
    changeLoginFlag ({ commit }, newLoginFlag) {
      console.log('$store changeLoginFlag: ', newLoginFlag)

      // 调用 mutations 里的相应方法，传值过去
      commit('changeLoginFlagMutation', newLoginFlag)
    },
    // 在 Index.vue 的 created 生命周期里面触发，AJAX 异步获取数据
    getIndexPageDataAction ({ commit }) {
      console.log('$store getIndexPageDataAction')
      axios.get('/')
        .then((response) => {
          // 解析 response.data
          console.log(response)
          let result = response.data
          let newIndexPageData = {}

          newIndexPageData.ccn_experts = JSON.parse(result.ccn_experts)
          newIndexPageData.hottest_five_organizations = []
          newIndexPageData.hottest_five_experts = []

          let ccnExpertsTemp = newIndexPageData.ccn_experts
          ccnExpertsTemp.forEach(function (ccnExpertsItem, ccnExpertsIndex) {
            ccnExpertsItem.experts.forEach(function (expertItem, index) {
              newIndexPageData.ccn_experts[ccnExpertsIndex].experts[index] = JSON.parse(expertItem)
            })
          })

          let hottestFiveOrganizationsTemp = JSON.parse(result.hottest_five_organizations)
          hottestFiveOrganizationsTemp.forEach(function (item, index) {
            newIndexPageData.hottest_five_organizations.push(JSON.parse(item))
          })

          let hottestFiveExpertsTemp = JSON.parse(result.hottest_five_experts)
          hottestFiveExpertsTemp.forEach(function (item, index) {
            newIndexPageData.hottest_five_experts.push(JSON.parse(item))
          })

          // 调用 mutations 里的相应方法，传值过去
          commit('changeIndexPageDataMutation', newIndexPageData)
        })
        .catch((error) => {
          console.log(error)
        })
    },
    // 在 Index.vue 中查询专家后调用，AJAX 异步获取数据
    getExpertQueryListAction ({ commit }, qi, qt, qs, r, f, rc, org) {
      console.log('$store getExpertQueryListAction')
      let serverUrl = '/expert_list'

      let postData = {}
      if (qt === 'normal') {
        postData.query_input = qi
        postData.query_type = qt
        postData.query_selection = qs
      } else if (qt === 'advanced') {
        postData.query_type = qt
        postData.researcher_input = r
        postData.field_input = f
        postData.research_content_input = rc
        postData.organization_input = org
      } else {
        console.error('query_type error!!!')
        return false
      }

      console.log(postData)

      axios.post(serverUrl, postData)
        .then((response) => {
          // 解析 response.data
          console.log(response)
          let result = response.data

          let newExpertQueryList = []
          let newExpertQueryListPageTotal = 1
          let newExpertQueryListPageCurrent = 1

          if (!result || result === '' || result.length === 0) {
            newExpertQueryListPageTotal = 1
          } else {
            newExpertQueryListPageTotal = Math.floor(result.length / 20) + 1
          }
          newExpertQueryList = JSON.parse('[' + result + ']')

          // 调用 mutations 里的 changeExpertQueryListMutation 方法，传值过去
          commit('changeExpertQueryListMutation', newExpertQueryList)
          commit('changeExpertQueryListPageTotalMutation', newExpertQueryListPageTotal)
          commit('changeExpertQueryListPageCurrentMutation', newExpertQueryListPageCurrent)
        })
        .catch((error) => {
          console.log(error)
        })
    },
    // 在 Detail.vue 的 created 生命周期里面触发，AJAX 异步获取数据
    getDetailPageDataAction ({ commit }, expertId) {
      console.log('$store getDetailPageDataAction', expertId)
      axios.get('/expert_detail/' + expertId)
        .then((response) => {
          // 解析 response.data
          console.log(response)
          let result = response.data

          let newDetailPageData = {}
          let newPaperPageTotal
          let newPaperPageCurrent

          newDetailPageData.expert_basic = JSON.parse(result.expert_basic)
          newDetailPageData.expert_academic = JSON.parse(result.expert_academic)
          newDetailPageData.papers = JSON.parse(result.papers)
          newDetailPageData.co_experts_info = JSON.parse(result.co_experts_info)
          newDetailPageData.influence_info = JSON.parse(result.influence_info)

          newPaperPageTotal = Math.floor(newDetailPageData.papers.length / 10) + 1
          newPaperPageCurrent = 1
          // 使paperPageCurrent和paperPageTotal为十进制整数
          newPaperPageTotal = parseInt(newPaperPageTotal, 10)
          newPaperPageCurrent = parseInt(newPaperPageCurrent, 10)

          // 调用 mutations 里的 changeDetailPageDataMutation 方法，传值过去
          commit('changeDetailPageDataMutation', newDetailPageData)
          commit('changePaperPageTotalMutation', newPaperPageTotal)
          commit('changePaperPageCurrentMutation', newPaperPageCurrent)
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }
})

Vue.config.productionTip = false

export default new Vue({
  el: '#app',
  // router,
  axios: axios,
  // 把实例化后的 vue-router 插入到根 Vue 内
  router: router,
  // 全局使用之前已经实例化了的 vuex 状态管理器 store
  store: store,
  components: { App },
  template: '<App/>'
})



