    // 压入历史栈
    pushState (query) {
      let _this = this
      let popStateData = {}

      popStateData.detailPageData = _this.detailPageData
      popStateData.paperPageTotal = _this.paperPageTotal
      popStateData.paperPageCurrent = _this.paperPageCurrent
      popStateData.expertIdCurrent = _this.expertIdCurrent
      popStateData.favResult = _this.favResult
      popStateData.favFlag = _this.favFlag
      popStateData.showMenu = _this.showMenu
      console.log(popStateData)

      if (location.href.indexOf('#') !== -1) {
        console.log('压入历史栈: ' + location.href.split('#')[0] + '#/' + query)
        history.pushState(popStateData, document.title, location.href.split('#')[0] + '#/' + query)
      } else {
        console.log('压入历史栈: ' + location.href)
        history.pushState(popStateData, document.title, location.href)
      }
    },
    // 获取 id 参数
    getExpertId () {
      let expertId = this.$route.params.expert_id
      if ((typeof expertId) === 'undefined') {
        // alert('Error: id参数不合法')
        console.error('Error: id参数不合法')
        return false
      }
      // 检验 id 合法性(全是数字)
      if (isNaN(expertId)) {
        // alert('Error: id参数不合法')
        console.error('Error: id参数不合法')
        return false
      }
      this.expertIdCurrent = expertId

      // this.loadDetailPage()
    },
    // AJAX 查询专家详情获得数据,成功后渲染页面
    loadDetailPage () {
      'use strict'
      let _this = this

      let expertId = this.expertIdCurrent
      let startDate = new Date()
      let endDate = startDate

      let serverUrl = '/expert_detail'

      let postData = {}
      if (!expertId || expertId === '') {
        return false
      } else {
        postData.id = expertId
        postData.current_page = 1
      }

      // HTTP GET (JQuery Ajax)
      $.ajax({
        type: 'GET',
        url: serverUrl,
        data: postData,
        dataType: 'json',
        beforeSend: function (xhr) {
          endDate = new Date()
          console.log('初始耗时：', endDate - startDate)

          // console.log('$.ajax beforeSend, state = ' + xhr.status)
          // 出现loading动画
          $('#intro .load-container').css({'display': 'block'})
        },
        success: function (result, status, xhr) {
          endDate = new Date()
          console.log('初始耗时 + AJAX耗时：', endDate - startDate)

          console.log('$.ajax success, state = ' + xhr.status)
          // 隐藏loading动画
          $('#intro .load-container').css({'display': 'none'})

          if (!result || result === '' || result.length === 0) {
            alert('抱歉，该专家无信息。')
            console.log('抱歉，该专家无信息。')
            return false
          }

          // 接收数据初始化
          _this.detailPageData = {}
          _this.paperPageTotal = 0
          _this.paperPageCurrent = 0

          // 解析JSON数据
          _this.favResult = result.fav_result

          _this.detailPageData.expert_basic = JSON.parse(result.expert_basic)
          _this.detailPageData.expert_academic = JSON.parse(result.expert_academic)
          _this.detailPageData.papers = JSON.parse(result.papers)
          _this.detailPageData.co_experts_info = JSON.parse(result.co_experts_info)
          _this.detailPageData.influence_info = JSON.parse(result.influence_info)
          _this.detailPageData.influence_time = JSON.parse(result.influence_time)
          _this.detailPageData.paper_time = JSON.parse(result.paper_time)

          // 由于数据库中存储的 unicode 代码是 u1234 格式而不是正常的 \u1234 格式，因此需要转换
          let opinionTemp = JSON.parse(result.opinion_raw)
          let reg = new RegExp('u', 'g')
          let opinionSocietyString
          if ((opinionTemp.opinion_society != null) && ((typeof opinionTemp.opinion_society) !== 'undefined')) {
            try {
              opinionSocietyString = opinionTemp.opinion_society.replace(reg, '\\u')
              opinionTemp.opinion_society = JSON.parse(opinionSocietyString)
            } catch (e) {
              console.log(e.message())
            }
          } else {
            opinionTemp.opinion_society = ''
          }
          console.log(opinionTemp)
          _this.detailPageData.opinion_raw = opinionTemp

          let tempResult = []
          let temp = JSON.parse(result.same_hometown_experts)
          if (Array.isArray(temp) && temp.length > 0) {
            temp.forEach(function (item) {
              tempResult.push(JSON.parse(item))
            })
          }
          _this.detailPageData.same_hometown_experts = tempResult

          tempResult = []
          temp = JSON.parse(result.similar_interest_experts)
          if (Array.isArray(temp) && temp.length > 0) {
            temp.forEach(function (item) {
              tempResult.push(JSON.parse(item))
            })
          }
          _this.detailPageData.similar_interest_experts = tempResult

          _this.detailPageData.paperNumber = JSON.parse(result.paper_num)
          if (_this.detailPageData.paperNumber <= 0) {
            _this.detailPageData.paperNumber = 0
            _this.paperPageTotal = 0
          } else {
            _this.paperPageTotal = Math.floor(_this.detailPageData.paperNumber / 10) + 1
          }
          _this.paperPageCurrent = 1
          // 使paperPageCurrent和paperPageTotal为十进制整数
          _this.paperPageTotal = parseInt(_this.paperPageTotal, 10)
          _this.paperPageCurrent = parseInt(_this.paperPageCurrent, 10)

          console.log(_this.detailPageData)
          console.log(JSON.parse(_this.detailPageData.papers[0]))

          // 获取 AJAX 数据
          _this.getThisData(_this)

          // 压入历史栈
          _this.pushState('expert_detail/' + expertId)

          // 使用数据渲染页面
          // result = _this.detailPageData
          _this.renderDetailPage()

          endDate = new Date()
          console.log('初始耗时 + AJAX耗时 + 渲染耗时：', endDate - startDate)

          return true
        },
        error: function (xhr, status, error) {
          console.log('$.ajax error, state = ' + xhr.status)
          // 隐藏loading动画
          $('#intro .load-container').css({'display': 'none'})
          alert('HTTP GET Error! status=' + status + '&error=' + error +
            '&statusCode=' + xhr.status + '&responseText=' + xhr.responseText +
            '&readyState=' + xhr.readyState)
        },
        statusCode: {
          // 当响应对应的状态码时，执行对应的回调函数
          200: function () {
            console.log('200: 请求成功')
          },
          404: function () {
            console.log('404: 找不到页面')
            alert('404: 找不到页面')
          },
          500: function () {
            console.log('500: 服务器错误')
            alert('500: 服务器错误')
          }
        }
      })
    },
    // 获取 AJAX 数据
    getThisData (_this) {
      this.detailPageData = _this.detailPageData
      this.paperPageTotal = _this.paperPageTotal
      this.paperPageCurrent = _this.paperPageCurrent

      console.log(this.detailPageData)
      console.log(this.paperPageTotal)
      console.log(this.paperPageCurrent)
    },
    // 渲染详情页
    renderDetailPage () {
      'use strict'
      let _this = this
      let resultAjax = this.detailPageData

      // 绘制发文量变化曲线图
      this.drawAcademicEcharts(resultAjax)

      // 绘制影响力变化曲线图
      this.drawInfluenceEcharts(resultAjax)

      // 绘制专家关系图
      this.relationshipNet(resultAjax)

      // TODO 展示专家观点
      this.showOpinion(resultAjax)

      // 增添其它信息
      let jqResearcherAcademicContent = $('#researcher-academic-content > table > tbody > tr:first-child')
      let jqThisResearcherName = $('.this-researcher-name')
      let jqThisResearcherUniversity = $('.this-researcher-university')
      let jqThisResearcherCollege = $('.this-researcher-college')
      // let jqThisResearcherSubject = $('#researcher-academic-content .this-researcher-subject')
      let jqThisResearcherTheme = $('#researcher-academic-content .this-researcher-theme')
      let jqThisResearcherPapers = $('#researcher-papers ul.papers')
      let jqThisResearcherImage = $('img.researcher-image')
      let jqThisResearcherResume = $('#researcher-resume article.resume')
      let jqCurrentPage = $('#current-page')
      let jqTotalPages = $('#total-pages')
      let jqPaperPreviousPage = $('#paper-previous-page')
      let jqPaperNextPage = $('#paper-next-page')

      let htmlTemplate =
        '<td>' + resultAjax.expert_academic.amount1 + '</td>' +
        '<td>' + (resultAjax.expert_academic.amount2 || '无数据') + '</td>' +
        '<td>' + (resultAjax.expert_academic.h_index || '无数据') + '</td>' +
        '<td>' + (resultAjax.expert_academic.core || '无数据') + '</td>' +
        '<td>' + (resultAjax.expert_academic.cssci || '无数据') + '</td>' +
        '<td>' + (resultAjax.expert_academic.rdfybkzl || '无数据') + '</td>'
      jqResearcherAcademicContent.empty()
        .append(htmlTemplate)

      jqThisResearcherName.empty()
        .append(resultAjax.expert_academic.name || '无姓名')

      jqThisResearcherUniversity.empty()
        .append(resultAjax.expert_basic.university || '无所属学校信息')

      jqThisResearcherCollege.empty()
        .append(resultAjax.expert_basic.college || '无所属学院信息')

      // jqThisResearcherSubject.empty()
      //   .append(resultAjax.expert_basic.sub_list || '无钻研领域信息')

      jqThisResearcherTheme.empty()
        .append(resultAjax.expert_basic.theme_list || '无研究内容信息')

      jqCurrentPage.empty()
        .append(this.paperPageCurrent)

      jqTotalPages.empty()
        .append(this.paperPageTotal)

      // 使上一页按钮不可选
      if (!jqPaperPreviousPage.hasClass('disabled')) {
        jqPaperPreviousPage.addClass('disabled')
      }

      // 若总共仅一页,则使下一页按钮也不可选
      if (this.paperPageTotal === 1 && !jqPaperNextPage.hasClass('disabled')) {
        jqPaperNextPage.addClass('disabled')
      }

      // 若数据库中有学者的头像,则将页面中的默认头像更换掉
      let expertImage = resultAjax.expert_basic.img_url
      if (expertImage && expertImage !== '') {
        jqThisResearcherImage.attr({'src': expertImage})
      } else {
        // 否则改为默认头像
        jqThisResearcherImage.attr({'src': '../../static/asset/image/default_expert_image.png'})
      }

      // 学者简历信息（处理简历，避免过长）
      let expertResume = resultAjax.expert_basic.resume // .replace(/[\n\r]/g, '')
      let expertResumeShort = ''
      let charLimit = 1000
      if ((expertResume != null) && ((typeof expertResume) !== 'undefined')) {
        try {
          expertResumeShort = expertResume
            // .replace(/<br[ ]*\/[ ]*>/g, '')
            .substring(0, charLimit)
          expertResumeShort = expertResumeShort + '......'
        } catch (e) {
          console.log(e.message())
        }

        if (expertResume.length <= charLimit) {
          // 如果简历字符串达不到限制字符长度，就不必增加"查看更多"按钮
          if (expertResume && expertResume !== '') {
            jqThisResearcherResume.empty().append('<p class="expert-resume-show"><strong>学者简历：</strong>' +
              expertResume + '</p>')
          } else {
            jqThisResearcherResume.empty().append('<p class="expert-resume-show">无该学者简历数据.</p>')
          }
        } else {
          // 如果简历字符串超过了限制字符长度，就要增加"查看更多"按钮
          if (expertResumeShort && expertResumeShort !== '') {
            jqThisResearcherResume.empty().append('<p class="expert-resume-show"><strong>学者简历：</strong>' +
              expertResumeShort + '</p>' + '<a class="toggle-show-resume">查看更多</a>')
          } else {
            jqThisResearcherResume.empty().append('<p class="expert-resume-show">无该学者简历数据.</p>')
          }
          $('#researcher-resume article.resume a.toggle-show-resume')
            .unbind('click')
            .css({
              'text-decoration': 'underline',
              'cursor': 'pointer'
            })
            .click(function () {
              _this.toggleShowResume()
            })
        }
      } else {
        jqThisResearcherResume.empty().append('<p class="expert-resume-show">无该学者简历数据.</p>')
      }

      // 展示前10篇论文(每页10篇,页码从1开始)
      let paperItem
      htmlTemplate = ''
      let firstPagePapers = resultAjax.papers.slice(0, 10)
      firstPagePapers.forEach(function (item, index) {
        // 解析数据为JSON格式
        paperItem = JSON.parse(item)

        // 取authors字段的前五名作者
        let paperAuthorsName = paperItem.authors.split(' ').slice(0, 5)

        // 获取作者id信息(在数据库中存在的作者才有id,否则只有名字)
        let paperAuthorsId = []
        if (paperItem.author1 && (paperItem.author1 !== '')) {
          paperAuthorsId.push(paperItem.author1)
        } else {
          paperAuthorsId.push('')
        }
        if (paperItem.author2 && (paperItem.author2 !== '')) {
          paperAuthorsId.push(paperItem.author2)
        } else {
          paperAuthorsId.push('')
        }
        if (paperItem.author3 && (paperItem.author3 !== '')) {
          paperAuthorsId.push(paperItem.author3)
        } else {
          paperAuthorsId.push('')
        }
        if (paperItem.author4 && (paperItem.author4 !== '')) {
          paperAuthorsId.push(paperItem.author4)
        } else {
          paperAuthorsId.push('')
        }
        if (paperItem.author5 && (paperItem.author5 !== '')) {
          paperAuthorsId.push(paperItem.author5)
        } else {
          paperAuthorsId.push('')
        }

        // 设置html结点
        htmlTemplate += '<li><article><header><h3><a class="jump-paper-detail" data-id="' + paperItem.paper_id + '">' +
          paperItem.title + '</a></h3></header>'

        // 设置作者
        htmlTemplate += '<p>'
        let i
        for (i = 0; i < paperAuthorsName.length; i++) {
          if (paperAuthorsId[i] === '') {
            // 若作者无id,表示该作者不在数据库中,故不做链接跳转
            htmlTemplate += (paperAuthorsName[i] || paperAuthorsId[i]) + ' , '
          } else {
            htmlTemplate += '<a class="paper-author-item" data-id="' + paperAuthorsId[i] + '">' +
              (paperAuthorsName[i] || paperAuthorsId[i]) + '</a>' + ' , '
          }
        }
        // 截去字符串最后的' , '
        htmlTemplate = htmlTemplate.substring(0, (htmlTemplate.length - 3))

        // 设置其它论文信息
        htmlTemplate += ' | ' + (paperItem.type || '') + ' | ' + paperItem.source + '</p>'

        htmlTemplate += '<p><strong>' + '摘要</strong>: ' + paperItem.abstract + '</p>'

        htmlTemplate += '<p><strong>' + '关键字</strong>: ' + paperItem.keyword + '</p>'

        htmlTemplate += '</article></li>'
      })
      jqThisResearcherPapers.empty()
        .append(htmlTemplate)

      // 根据 favResult 判断用户是否已收藏该专家
      if (_this.favResult === '已收藏') {
        _this.favFlag = true
        $('a#star-researcher').empty()
          .append('&hearts;已收藏')
          .unbind('click')
          .click(function () {
            _this.starExpert()
          })
      } else if (_this.favResult === '未收藏') {
        _this.favFlag = false
        $('a#star-researcher').empty()
          .append('&loz;收藏专家')
          .unbind('click')
          .click(function () {
            _this.starExpert()
          })
      } else {
        console.log('fav_result 异常')
        _this.favFlag = false
      }

      // 增添点击事件
      $('#researcher-papers ul.papers a.paper-author-item').unbind('click').click(function () {
        _this.jumpToAnotherResearcher($(this).attr('data-id'))
      })

      $('#researcher-papers ul.papers a.jump-paper-detail').unbind('click').click(function () {
        _this.jumpPaperDetailPage($(this).attr('data-id'))
      })
    },
