    },
    // 绘制 Echarts 关系图
    drawNetworkEcharts (coExpertArray) {
      // console.log(coExpertArray)
      let resultAjax = this.detailPageData
      let _this = this

      // 关系值归一化，y = sigmoid(x) = 1 / (1 + e ^ (-x))
      // 1/(1+e^(5-x)) 会使得当x在 [0, 10] 的区间时，分值以 sigmoid 函数形态从约等于0增加到约等于1，之后的函数值都很接近1
      coExpertArray.forEach(function (item, index) {
        let score = parseFloat(item.co_score)
        if (isNaN(score)) {
          // 数据有误！！！
          console.error('关系值数据不是数字！！！')
          // 设置一个默认数值
          score = 2
        } else {
          // 数据库100分时，score为10，结果约为1
          score = score / 10
        }
        score = 100 / (1 + Math.exp(5 - score))
        // console.log(score)
        coExpertArray[index].co_score = score.toFixed(2)
      })

      // 设置容器的宽高
      let jqRelationshipCanvasDiv = $('#relationship-picture-div')
      let jqRelationshipEcharts = $('div#relationship-echarts')

      let width = jqRelationshipCanvasDiv.width()
      let height = window.innerHeight / 8 * 5
      jqRelationshipEcharts.width(width)
      jqRelationshipEcharts.height(height)

      // 给容器增加边框
      jqRelationshipCanvasDiv.css({'border': '1px solid #aaa'})

      let coAgencyString = resultAjax.expert_academic.co_agency
      if ((coAgencyString != null) && ((typeof coAgencyString) !== 'undefined')) {
        try {
          coAgencyString = coAgencyString.replace(/\[/g, '')
          coAgencyString = coAgencyString.replace(/]/g, '')
          coAgencyString = coAgencyString.replace(/'/g, '')
          coAgencyString = coAgencyString.replace(/ /g, '')
        } catch (e) {
          console.log(e.message())
        }
      } else {
        coAgencyString = ''
      }

      let coAgencyList
      if (coAgencyString != null && coAgencyString.indexOf(',') !== -1) {
        coAgencyList = coAgencyString.split(',')
      } else {
        coAgencyList = []
      }

      let networkInfo = {
        // 关系网类别
        categories: [
          {
            name: resultAjax.expert_basic.name
          }
        ],
        // 节点
        // category 与关系网类别索引对应，id 是自动生成的
        nodes: [
          {
            category: 0,
            name: resultAjax.expert_basic.name,
            value: '本页专家',
            id: 0,
            expert_id: resultAjax.expert_basic.id,
            university: resultAjax.expert_basic.university,
            college: resultAjax.expert_basic.college,
            resume: resultAjax.expert_basic.resume,
            img_url: resultAjax.expert_basic.img_url,
            sub_list: resultAjax.expert_basic.sub_list,
            theme_list: resultAjax.expert_basic.theme_list
          }
        ],
        // 节点之间连线
        // source起始节点，0表示第一个节点
        // target目标节点，1表示与索引(id)为1的节点进行连接
        links: []
      }

      // 把相关机构的 node link 信息写入关系网
      let nodeId = 1
      let categoryId = 1
      networkInfo.categories.push({name: '相关机构'})
      coAgencyList.forEach(function (item, index) {
        networkInfo.nodes.push({
          category: categoryId,
          name: item,
          value: '相关机构',
          id: nodeId
        })
        networkInfo.links.push({
          source: nodeId,
          target: 0, // 连线目标都是 0
          co_score: item.co_score, // TODO 目前还没有机构关系分数
          value: '相关机构'
        })
        nodeId++
      })
      categoryId++

      // 把同乡关系的 node link 信息写入关系网
      networkInfo.categories.push({name: '同乡学者'})
      let sameHometownLimit = 10
      let sameHometownExperts = this.detailPageData.same_hometown_experts
      for (let i = 0; i < sameHometownExperts.length; i++) {
        let coExpert = sameHometownExperts[i]
        // console.log(coExpert)
        if (i > sameHometownLimit) {
          break
        }
        if (coExpert.id !== _this.detailPageData.expert_basic.id) {
          networkInfo.nodes.push({
            category: categoryId,
            name: coExpert.name,
            value: '同乡学者',
            id: nodeId,
            expert_id: coExpert.id,
            resume: coExpert.resume,
            img_url: coExpert.img_url
          })
          networkInfo.links.push({
            source: nodeId,
            target: 0, // 连线目标都是 0
            value: '同乡学者'
          })
          nodeId++
        }
      }
      categoryId++

      // TODO 把同学关系的 node link 信息写入关系网
      networkInfo.categories.push({name: '同学学者'})
