      //   category: categoryId,
      //   name: coExpert.name,
      //   value: '同学学者',
      //   id: nodeId,
      //   expert_id: coExpert.id,
      //   resume: coExpert.resume,
      //   img_url: coExpert.img_url
      // })
      // networkInfo.links.push({
      //   source: nodeId,
      //   target: 0, // 连线目标都是 0
      //   value: '同学学者'
      // })
      // nodeId++
      categoryId++

      // TODO 把同事关系的 node link 信息写入关系网
      networkInfo.categories.push({name: '同事学者'})
      // networkInfo.nodes.push({
      //   category: categoryId,
      //   name: coExpert.name,
      //   value: '同事学者',
      //   id: nodeId,
      //   expert_id: coExpert.id,
      //   resume: coExpert.resume,
      //   img_url: coExpert.img_url
      // })
      // networkInfo.links.push({
      //   source: nodeId,
      //   target: 0, // 连线目标都是 0
      //   value: '同事学者'
      // })
      // nodeId++
      categoryId++

      // 把相似兴趣关系的 node link 信息写入关系网
      networkInfo.categories.push({name: '相似兴趣学者'})
      let similarInterestLimit = 10
      let similarInterestExperts = this.detailPageData.similar_interest_experts
      for (let i = 0; i < similarInterestExperts.length; i++) {
        let coExpert = similarInterestExperts[i]
        // console.log(coExpert)
        if (i > similarInterestLimit) {
          break
        }
        if (coExpert.id !== _this.detailPageData.expert_basic.id) {
          networkInfo.nodes.push({
            category: categoryId,
