            value: '相似兴趣学者',
            id: nodeId,
            expert_id: coExpert.id,
            resume: coExpert.resume,
            img_url: coExpert.img_url
          })
          networkInfo.links.push({
            source: nodeId,
            target: 0, // 连线目标都是 0
            value: '相似兴趣学者'
          })
          nodeId++
        }
      }
      categoryId++

      // 获取所有合作学者的年份
      let yearList = [] // 相关专家的年份列表
      coExpertArray.forEach(function (item, index) {
        if (yearList.length === 0) {
          yearList.push(item.co_year)
        } else {
          if (yearList.indexOf(item.co_year) === -1) {
            yearList.push(item.co_year)
          }
        }
      })
      yearList = yearList.sort().reverse()

      // 至多只取最近十个年份的合作学者
      let yearLimit = 10
      if (yearList.length > yearLimit) {
        yearList = yearList.slice(0, yearLimit)
      }

      // 获取当前所选的年份
      // let relationshipYear = ''
      // jqRelationshipYear = $('select#relationship-year')
      // relationshipYear = jqRelationshipYear.val()

      // 初始状态选择最近的一年的论文合作学者
      // jqRelationshipType = $('select#relationship-type option:selected')
      // relationshipType = jqRelationshipType.val()

      // 每一年一个类，写入关系网
      let flag = true
      yearList.forEach(function (thisYear, index) {
        flag = true
        coExpertArray.forEach(function (coExpert, index) {
          // console.log(coExpert)
          if (coExpert.co_year === thisYear) {
            if (flag) {
              networkInfo.categories.push({name: thisYear + '年 学术合作学者'})
              flag = false
            }
            networkInfo.nodes.push({
              category: categoryId,
              name: coExpert.name,
              value: coExpert.co_score, // 节点 value，据此设置节点大小，即 symbol 值
              id: nodeId,
              expert_id: coExpert.id,
              co_score: coExpert.co_score,
              co_year: coExpert.co_year,
              resume: coExpert.resume,
              img_url: coExpert.img_url
            })
            networkInfo.links.push({
              source: nodeId,
              target: 0, // 连线目标都是 0
              co_score: coExpert.co_score,
              value: '论文合作学者'
            })
            nodeId++
          }
        })
        categoryId++
      })

      console.log(networkInfo)

      // 基于准备好的 dom，初始化 echarts 实例
      let myChart = Echarts.init(document.getElementById('relationship-echarts'))

      // 指定图表的配置项和数据 http://echarts.baidu.com/examples/data/asset/data/les-miserables.gexf
      // app.title = '力引导布局'

      // myChart.showLoading()
      // myChart.hideLoading()

      // 设置初始只显示相关机构和最近一年的合作学者
