          roam: false,
          nodeScaleRatio: 0, // 鼠标漫游缩放时节点的相应缩放比例，当设为0时节点不随着鼠标的缩放而缩放
          draggable: true, // 节点是否可拖拽，只在使用力引导布局的时候有用。
          focusNodeAdjacency: true, // 是否在鼠标移到节点上的时候突出显示节点以及节点的边和邻接节点。
          // animation: false,
          label: {
            normal: {
              show: true,
              position: 'right'
            }
          },
          force: {
            layoutAnimation: true,
            // xAxisIndex : 0, // x轴坐标, 有多种坐标系轴坐标选项
            // yAxisIndex : 0, // y轴坐标
            gravity: 0.03, // 节点受到的向中心的引力因子。该值越大节点越往中心点靠拢。
            edgeLength: 70, // 边的两个节点之间的距离，这个距离也会受 repulsion。[10, 50] 。值越小则长度越长
            repulsion: 150 // 节点之间的斥力因子。支持数组表达斥力范围，值越大斥力越大。
          },
          // node数据
          data: networkInfo.nodes.map(function (node, index) {
            node.id = index
            return node
          }),
          // 关系网类别，可以写多组
          categories: networkInfo.categories,
          // link数据
          edges: networkInfo.links,
          // 边两端的标记类型，可以是一个数组分别指定两端，也可以是单个统一指定。默认不显示标记，常见的可以设置为箭头，
          // 如：edgeSymbol: ['circle', 'arrow']
          edgeSymbol: ['none', 'none'],
          edgeSymbolSize: 10, // 边两端的标记大小，可以是一个数组分别指定两端，也可以是单个统一指定。

          // 关系图节点标记的图形。ECharts 提供的标记类型包括 'circle'(圆形), 'rect'（矩形）,
          // 'roundRect'（圆角矩形）, 'triangle'（三角形）, 'diamond'（菱形）, 'pin'（大头针）, 'arrow'（箭头）.
          // 也可以通过 'image://url' 设置为图片，其中 url 为图片的链接。'path:// 这种方式可以任意改变颜色并且抗锯齿
          symbol: 'circle',

          // 关系图节点标记的大小，可以设置成诸如 10 这样单一的数字，也可以用数组分开表示宽和高，例如 [20, 10] 表示标记宽为20高为10。
          // 如果需要每个数据的图形大小不一样，可以设置为如下格式的回调函数：
