              return 15
            }
            // 如果是 2 类节点，则为同乡学者
            if (data.data.category === 2) {
              return 20
            }
            // 如果是 3 类节点，则为相似兴趣学者
            if (data.data.category === 3) {
              return 20
            }
            // 检查 value 值类型
            let size = 10
            if ((typeof value) !== 'number') {
              size = value / 3
            } else if ((typeof value) !== 'string') {
              size = parseFloat(value) / 3
            } else {
              console.error('value值错误！！！')
              return 10
            }
            // 设置最低显示大小为 10
            if (size < 10) {
              size = 10
            }
            return size
          }

