                if ((handler.data.co_score != null) && ((typeof handler.data.co_score) !== 'undefined')) {
                  html += '<p><strong>关系分数</strong>：' + handler.data.co_score + '</p>'
                }
                if (resume !== '') {
                  html += '<p><strong>专家简历</strong>：' + resume + '</p>'
                }
              }

              html += '</div>'
              return html
