    // 从当前的关系网络选项，获取 nodeList
    getNodeListD3 (coExpertArray) {
      let resultAjax = this.detailPageData
      let _this = this

      // 获取当前所选的年份
      let relationshipYear = ''
      let jqRelationshipYear = $('select#relationship-year')
      relationshipYear = jqRelationshipYear.val()

      // 初始状态选择最近的一年的论文合作学者
      let jqRelationshipType = $('select#relationship-type option:selected')
      let relationshipType = jqRelationshipType.val()

      // 关系网络文字描述
      let jqRelationshipDiv = $('#relationship-div')
      let nodeList = []
      if (relationshipType === 'co_expert_paper') {
        // 如果当前选择了"论文合作学者"，那么使年份选择框可用
        if (jqRelationshipYear.attr('disabled') !== false) {
          jqRelationshipYear.attr('disabled', false)
        }
        // 点击学者名跳转到该学者详情页
        let relationshipHtml = '<h3>' + relationshipYear + '年 论文合作学者</h3><p>'
        coExpertArray.forEach(function (item, index) {
          // 对该学者在所学年份的论文合作学者设置跳转锚点
          if (item.co_year === relationshipYear) {
            relationshipHtml += '<a class="jump-author" data-id="' + item.id + '">' +
              item.name + '</a>' + ' , '
            // TODO 目前仅把学者 id 加入到 nodeList
            nodeList.push(item.id)
          }
        })
        relationshipHtml = relationshipHtml.substring(0, (relationshipHtml.length - 3)) // 截去字符串最后的' , '
        relationshipHtml += '</p>'

        jqRelationshipDiv.empty()
          .append(relationshipHtml)

        // 增添点击事件
        $('#relationship-div a.jump-author').unbind('click').click(function () {
          _this.jumpToAnotherResearcher($(this).attr('data-id'))
        })
      } else if (relationshipType === 'co_agency') {
        // 如果当前选择了"相关机构"，那么使年份选择框不可用
        if (jqRelationshipYear.attr('disabled') !== true) {
          jqRelationshipYear.attr('disabled', true)
        }
        let coAgencyString = resultAjax.expert_academic.co_agency
        coAgencyString = coAgencyString.replace(/\[/g, '')
        coAgencyString = coAgencyString.replace(/]/g, '')
        coAgencyString = coAgencyString.replace(/'/g, '')
        coAgencyString = coAgencyString.replace(/ /g, '')

        nodeList = coAgencyString.split(',')

        // TODO 点击跳转机构页
        let relationshipHtml = '<h3>相关机构</h3><p>'
        nodeList.forEach(function (item, index) {
          relationshipHtml += item + ' , '
        })
        relationshipHtml = relationshipHtml.substring(0, (relationshipHtml.length - 3)) // 截去字符串最后的' , '
        relationshipHtml += '</p>'

        jqRelationshipDiv.empty()
        jqRelationshipDiv.append(relationshipHtml)
      }

      // console.log(nodeList)
      return nodeList
    },
    // D3.js 绘力导向图
    drawNetworkD3 (nodeList) {
      let resultAjax = this.detailPageData
      // 关系网络json
      let nodeId = 0
      let relationshipNetwork = {
        'nodes': {
          'center_node': [
            {
              'id': nodeId,
              'node_expert_id': resultAjax.expert_basic.id,
              'group': 0,
              'color': 'black'
            }
          ],
          'relative_node': [
          ]
        },
        'links': {
          'relative_link': [
          ]
        }
      }

      // 把结点写入 relationshipNetwork
      nodeId = 1
      nodeList.forEach(function (item, index) {
        let coNode = {}
        coNode.id = nodeId++
        coNode.node_expert_id = item
        coNode.group = 1
        coNode.color = '#add8e6' // light blue TODO 换成专家头像
        relationshipNetwork.nodes.relative_node.push(coNode)
      })

      // 把边写入 relationshipNetwork
      nodeId = 1
      nodeList.forEach(function (item, index) {
        let coLink = {}
        coLink.source = nodeId++
        coLink.target = 0
        coLink.stroke_width = 1 // TODO 根据关系紧密程度来设置线段
        relationshipNetwork.links.relative_link.push(coLink)
      })

      console.log(relationshipNetwork)

      let jqRelationshipCanvasDiv = $('#relationship-picture-div')

      let jqRelationshipSvg = $('#relationship-svg')

      let relationshipSvgHtml = '<defs>' +
        '<pattern id="default-expert-avatar" width="100%" height="100%" patternContentUnits="objectBoundingBox">' +
        '<image width="1" height="1" xlink:href="../../static/asset/image/default_expert_image.png" />' +
        '</pattern>' +
        '<style>circle, rect {stroke: #ff9900 stroke-width: 5px}' +
        '</style></defs>'

      jqRelationshipSvg.empty()
        .append(relationshipSvgHtml)

      let width = jqRelationshipCanvasDiv.width()
      let height = window.innerHeight / 2
      jqRelationshipSvg.width(width)
      jqRelationshipSvg.height(height)

      let svg = D3.select('#relationship-svg')

      let simulation = D3.forceSimulation()
        .force('link', D3.forceLink().id(function (item, index) {
          return item.id
        }).distance(height / 8 * 3))
        .force('charge', D3.forceManyBody())
        .force('center', D3.forceCenter(width / 2, height / 2))

      let allNodes = []
      allNodes = allNodes.concat(relationshipNetwork.nodes.center_node)
      allNodes = allNodes.concat(relationshipNetwork.nodes.relative_node)

      let agencyLink = relationshipNetwork.links.relative_link
      let allLinks = []
      allLinks = allLinks.concat(agencyLink)

      console.log(allNodes)
      console.log(allLinks)

      simulation
        .nodes(allNodes)
        .on('tick', Ticked)

      simulation.force('link')
        .links(allLinks)

      let link = svg.append('g')
        .attr('class', 'svg-links')
        .selectAll('line')
        .data(allLinks)
        .enter().append('line')
        // TODO stroke-width 属性可以用于表达关系紧密度
        .attr('stroke-width', function (d) { return d.stroke_width })
        .attr('stroke', '#aaa')

      let node = svg.append('g')
        .attr('class', 'svg-nodes')
        .selectAll('circle')
        .data(allNodes)
        .enter().append('circle') // 添加圆圈
        .attr('r', 25)
        // .attr("fill", function(d) {return d.color})
        .attr('fill', 'url(#default-expert-avatar)') // TODO 用头像填充照片
        .call(D3.drag()
          .on('start', dragstarted)
          .on('drag', dragged)
          .on('end', dragended)
        ).text(function (d) {
          return d.node_expert_id
        })

      function Ticked () {
        link
          .attr('x1', function (d) { return d.source.x })
          .attr('y1', function (d) { return d.source.y })
          .attr('x2', function (d) { return d.target.x })
          .attr('y2', function (d) { return d.target.y })

        node
          .attr('cx', function (d) { return d.x })
          .attr('cy', function (d) { return d.y })
      }

      function dragstarted (d) {
        if (!D3.event.active) simulation.alphaTarget(0.3).restart()
        d.fx = d.x
        d.fy = d.y
      }
      function dragged (d) {
        d.fx = D3.event.x
        d.fy = D3.event.y
      }
      function dragended (d) {
        if (!D3.event.active) simulation.alphaTarget(0)
        d.fx = null
        d.fy = null
      }

      // 给 svg circle 添加文字
      // let jq_svg_circle = $('#relationship-picture-div g.svg-nodes')
      // let jq_svg_line = $('#relationship-picture-div g.svg-links line')
      // jq_svg_circle.append('<text x="50" y="50" text-anchor="middle" stroke="#51c5cf" ' +
      //   'stroke-width="2px" dy=".3em">center</text>')
      // let svg_circle = D3.select('#relationship-svg g.svg-nodes circle')
      // let svg_line = D3.select('#relationship-svg g.svg-links line')
      // svg_circle.selectAll("circle")
      // .data(allNodes)
      // .enter().append("text")  // 添加文字
      // .attr("x", "100px").attr("y", "100px").attr("text-anchor", "middle")
      // .attr("stroke", "#51c5cf").attr("stroke-width", "2px").attr("dy", ".3em")
      // .text("center")

      // 给容器增加边框
      jqRelationshipCanvasDiv.css({'border': '1px solid #aaa'})
    },
    // TODO 关注本页学者
    starExpert () {
      'use strict'
      let _this = this
      let serverUrl = ''
      let data = {}

      // console.log(this.favResult)
      if (!this.favFlag) {
        serverUrl = 'add_fav'
        data.expert_id = this.expertIdCurrent

        // console.log(data)
        // HTTP Request (JQuery Ajax)
        $.ajax({
          type: 'GET',
          url: serverUrl,
          data: data,
          dataType: 'json',
          beforeSend: function (xhr) {
          },
          success: function (result, status, xhr) {
            console.log(result)
            if (!result || result === '' || result.length === 0) {
              alert('抱歉，收藏专家失败！')
              console.log('HTTP result 异常！')
              return false
            } else {
              let favResult = result.fav_result

              // 验证从后端返回的登录结果标志
              console.log('favResult === ' + favResult)
              if (favResult === '您已收藏该专家') {
                console.log('您已收藏该专家')
                alert('您已收藏该专家。')

                _this.favFlag = true
                $('a#star-researcher').empty()
                  .append('&hearts;已收藏')
              } else if (favResult === '添加成功') {
                console.log('添加成功')
                // alert('添加成功。')

                _this.favFlag = true
                // _this.pushState('expert_detail/' + this.expertIdCurrent)

                $('a#star-researcher').empty()
                  .append('&hearts;已收藏')
              } else if (favResult === '请先登陆') {
                console.log('请先登陆')
                alert('请先登陆。')
                // 打开菜单栏，光标聚焦于登录输入框
                _this.showMenu = false
                _this.toggleShowMenu()
                $('input.username').focus()
              } else {
                console.log('add_fav 返回数据异常')
                alert('抱歉，收藏专家失败！')
              }
            }
          },
          error: function (xhr, status, error) {
            alert('HTTP GET Error! status=' + status + '&error=' + error +
              '&statusCode=' + xhr.status + '&responseText=' + xhr.responseText +
              '&readyState=' + xhr.readyState)
          },
          statusCode: {
            // 当响应对应的状态码时，执行对应的回调函数
            200: function () {
              console.log('200: 请求成功')
            },
            404: function () {
              console.log('404: 找不到页面')
              alert('404: 找不到页面')
            },
            500: function () {
              console.log('500: 服务器错误')
              alert('500: 服务器错误')
            }
          }
        })
      } else {
        serverUrl = 'del_fav'

        data.expert_id = this.expertIdCurrent

        // HTTP Request (JQuery Ajax)
        $.ajax({
          type: 'GET',
          url: serverUrl,
          data: data,
          dataType: 'json',
          beforeSend: function (xhr) {
          },
          success: function (result, status, xhr) {
            console.log(result)
            if (!result || result === '' || result.length === 0) {
              alert('抱歉，取消收藏专家失败！')
              console.log('HTTP result 异常！')
              return false
            } else {
              let favResult = result.fav_result

              // 验证从后端返回的登录结果标志
              console.log('favResult === ' + favResult)
              if (favResult === '删除成功') {
                console.log('您已取消收藏该专家')
                // alert('您已收藏该专家。')

                _this.favFlag = false
                // _this.pushState('expert_detail/' + this.expertIdCurrent)

                $('a#star-researcher').empty()
                  .append('&loz;收藏专家')
              } else if (favResult === '请先登陆') {
                console.log('请先登陆')
                alert('请先登陆。')
                // 打开菜单栏，光标聚焦于登录输入框
                _this.showMenu = false
                _this.toggleShowMenu()
                $('input.username').focus()
              } else {
                console.log('取消收藏专家失败')
                alert('抱歉，取消收藏专家失败！')
              }
            }
          },
          error: function (xhr, status, error) {
            alert('HTTP GET Error! status=' + status + '&error=' + error +
              '&statusCode=' + xhr.status + '&responseText=' + xhr.responseText +
              '&readyState=' + xhr.readyState)
          },
          statusCode: {
            // 当响应对应的状态码时，执行对应的回调函数
            200: function () {
              console.log('200: 请求成功')
            },
            404: function () {
              console.log('404: 找不到页面')
              alert('404: 找不到页面')
            },
            500: function () {
              console.log('500: 服务器错误')
              alert('500: 服务器错误')
            }
          }
        })
      }
    },
    // 页面初始化
    initDetail () {
      'use strict'
      // Toast使用说明
      //
      // text：消息提示框的内容.
      // showHideTransition：消息提示框的动画效果。可取值：plain, fade, slide.
      // bgColor：背景颜色.
      // textColor：文字颜色.
      // allowToastClose：是否显示关闭按钮.
      // hideAfter：设置为false则消息提示框不自动关闭。设置为一个数值则在指定的毫秒之后自动关闭消息提示框.
      // stack：消息栈.
      // textAlign：文本对齐：left, right, center.
      // position：消息提示框的位置：bottom-left, bottom-right, bottom-center,
      //     top-left, top-right, top-center, mid-center.

      // 防止页面后退
      // window.history.pushState(null, null, document.URL)
      // window.addEventListener('popstate', function () {
      //   history.pushState(null, null, document.URL)
      // })

      // // 关系网络的 select option 改变时，更改力导向图
      // $('select.relationship-select').change(function () {
      // resultAjax = detailPageData
      // // 获取所有相关专家数据
      // let coExpertArray = []
      // resultAjax.co_experts_info.forEach(function (item, index) {
      //   coExpertArray.push(JSON.parse(item))
      // })
      // console.log(coExpertArray)
      // // 绘力导向图
      // drawNetworkEcharts(coExpertArray)
      // })

      let _this = this

      let jqueryWindow = $(window)
      let jqueryBody = $('body')
      // let jqueryMenu = $('.menu')
      let jquerySidebar = $('#sidebar')
      let jqueryMain = $('#main')

      // Breakpoints.
      breakpoints({
        xlarge: [ '1281px', '1680px' ],
        large: [ '981px', '1280px' ],
        medium: [ '737px', '980px' ],
        small: [ '481px', '736px' ],
        xsmall: [ null, '480px' ]
      })

      // Play initial animations on page load.
      jqueryWindow.on('load', function () {
        window.setTimeout(function () {
          jqueryBody.removeClass('is-preload')
        }, 100)
      })

      // Search (header).
      let jquerySearch = $('#search')
      let jquerySearchInput = jquerySearch.find('input')

      jqueryBody
        .on('click', '[href="#search"]', function (event) {
          event.preventDefault()

          // Not visible?
          if (!jquerySearch.hasClass('visible')) {
            // Reset form.
            jquerySearch[0].reset()

            // Show.
            jquerySearch.addClass('visible')

            // Focus input.
            jquerySearchInput.focus()
          }
        })

      jquerySearchInput
        .on('keydown', function (event) {
          if (event.keyCode === 27) { jquerySearchInput.blur() }
        })
        .on('blur', function () {
          window.setTimeout(function () {
            jquerySearch.removeClass('visible')
          }, 100)
        })

      // Intro.
      let intro = $('#intro')

      // Move to main on <=large, back to sidebar on >large.
      breakpoints.on('<=large', function () {
        intro.prependTo(jqueryMain)
      })

      breakpoints.on('>large', function () {
        intro.prependTo(jquerySidebar)
      })

      // 向服务器请求 session，判断用户是否已经登录
      let jqUsername = $('input.username')
      let jqPassword = $('input.password')
      let jqLogin = $('section.menu a.login')
      let jqRegister = $('section.menu a.register')

      let serverUrl = 'check_login'
      let data = {}

      // HTTP Request (JQuery Ajax)
      $.ajax({
        type: 'GET',
        url: serverUrl,
        data: data,
        dataType: 'json',
        beforeSend: function (xhr) {
        },
        success: function (result, status, xhr) {
          console.log(result)
          if (!result || result === '' || result.length === 0) {
            alert('抱歉，登录失败！')
            console.log('HTTP result 异常！')
            return false
          } else {
            // 解析JSON数据
            let checkLoginResult = result.checkLoginResult

            // 验证从后端返回的登录结果标志
            console.log('checkLoginResult === ' + checkLoginResult)
            if (checkLoginResult === 'success') {
              console.log('根据 Session 记录，用户已经登录。')

              let userId = result.userId
              if (userId !== null && userId !== undefined) {
                // 存储 userId 到 sessionStorage
                sessionStorage.setItem('user_id', userId)

                // 设置 UI
                jqUsername.hide()
                jqPassword.hide()
                jqLogin.text('注销')

                // 让注册按钮不可选
                if (!jqRegister.hasClass('disabled')) {
                  jqRegister.addClass('disabled')
                }

                // 修改 VUEX 里的登录状态
                if (!_this.$store.state.loginFlag) {
                  _this.$store.dispatch('changeLoginFlag', true)
                }
              }
            } else if (checkLoginResult === 'fail') {
              console.log('根据 Session 记录，用户尚未登录。')
            } else {
              console.log('查询 Session 记录，后端返回数据异常。')
            }
          }
        },
        error: function (xhr, status, error) {
          alert('HTTP GET Error! status=' + status + '&error=' + error +
            '&statusCode=' + xhr.status + '&responseText=' + xhr.responseText +
            '&readyState=' + xhr.readyState)
        },
        statusCode: {
          // 当响应对应的状态码时，执行对应的回调函数
          200: function () {
            console.log('200: 请求成功')
          },
          404: function () {
            console.log('404: 找不到页面')
            alert('404: 找不到页面')
          },
          500: function () {
            console.log('500: 服务器错误')
            alert('500: 服务器错误')
          }
        }
      })
    }
  }
}
</script>

