    margin: 0;
    text-transform: uppercase;
  }

  /* Table */

  .table-wrapper {
    -webkit-overflow-scrolling: touch;
    overflow-x: auto;
  }

  table {
    margin: 0 0 2em 0;
    width: 100%;
  }

  table tbody tr {
    border: solid 1px rgba(160, 160, 160, 0.3);
    border-left: 0;
    border-right: 0;
  }

  table tbody tr:nth-child(2n + 1) {
    background-color: rgba(160, 160, 160, 0.075);
  }

  table td {
    padding: 0.75em 0.75em;
  }

  table th {
    color: #3c3b3b;
    font-size: 0.9em;
