  }

  header.edms-header .links {
    -moz-flex: 1;
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
    border-left: solid 1px rgba(160, 160, 160, 0.3);
    height: inherit;
    line-height: inherit;
    margin-left: 1.5em;
    overflow: hidden;
    padding-left: 1.5em;
  }

  header.edms-header .links ul {
    height: inherit;
    line-height: inherit;
    list-style: none;
  }

  header.edms-header .links ul li {
    display: inline-block;
    border-left: solid 1px rgba(160, 160, 160, 0.3);
    /*line-height: 1;*/
    line-height: inherit;
    margin-left: 1em;
    padding-left: 1em;
    cursor: pointer;
    vertical-align: middle;
  }

  header.edms-header .links ul li:first-child {
    border-left: 0;
    margin-left: 0;
    padding-left: 0;
  }

  header.edms-header .links ul li:last-child {
    font-style: italic;
    font-weight: bold;
    color: #ff9841;
  }

  header.edms-header .links ul li a {
    border-bottom: 0;
    font-family: "Raleway", Helvetica, sans-serif;
    font-size: 0.7em;
    font-weight: 400;
    letter-spacing: 0.25em;
    text-transform: uppercase;
    vertical-align: middle;
  }

  header.edms-header .main {
    height: inherit;
    line-height: inherit;
    text-align: right;
  }

  header.edms-header .main ul {
    height: inherit;
    line-height: inherit;
    list-style: none;
  }

  header.edms-header .main ul li {
    display: inline-block;
    border-left: solid 1px rgba(160, 160, 160, 0.3);
    height: inherit;
    line-height: inherit;
    white-space: nowrap;
    vertical-align: middle;
  }

  header.edms-header .main ul li > * {
    display: block;
    float: left;
  }

  header.edms-header .main ul li > a {
    text-decoration: none;
    cursor: pointer;
    border-bottom: 0;
    color: #aaaaaa;
    overflow: hidden;
    position: relative;
    text-indent: 4em;
    width: 4em;
    vertical-align: middle;
  }

  header.edms-header .main ul li > a:before {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    font-family: FontAwesome, sans-serif;
    font-style: normal;
    font-weight: normal;
    text-transform: none !important;
  }

  header.edms-header .main ul li > a:before {
    display: block;
    height: inherit;
    left: 0;
    line-height: inherit;
    position: absolute;
    text-align: center;
    text-indent: 0;
    top: 0;
    width: inherit;
  }

  header.edms-header form {
    margin: 0;
