    height: 2.5em;
    position: relative;
    /*top: -2px;*/
    vertical-align: middle;
  }

  header.edms-header #search {
    -moz-transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
    -ms-transition: all 0.5s ease;
    transition: all 0.5s ease;
    max-width: 0;
    opacity: 0;
    overflow: hidden;
    padding: 0;
    white-space: nowrap;
  }

  header.edms-header #search input {
    width: 15em;
  }

  header.edms-header #search.visible {
    max-width: 17.5em;
    opacity: 1;
    padding: 0 0.5em 0 0;
  }

  @media screen and (max-width: 980px) {

    header.edms-header .links {
      /*display: none;*/
    }

