      height: 2.75em;
      line-height: 2.75em;
    }

    header.edms-header h1 {
      padding: 0 0 0 1em;
    }

    header.edms-header .main .search {
      /*display: none;*/
    }

  }

  /* Wrapper */

  #wrapper {
    display: -moz-flex;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    -moz-flex-direction: row-reverse;
    -webkit-flex-direction: row-reverse;
