      -0.11em -0.83em 0 -0.46em #a4dbf4,
      -0.11em -0.83em 0 -0.48em #a4dbf4;
    }
    30% {
      box-shadow:-0.11em -0.83em 0 -0.4em #eeb422,
      -0.51em -0.66em 0 -0.42em #43cd80,
      -0.75em -0.36em 0 -0.44em #5bc5f4,
      -0.83em -0.03em 0 -0.46em #5bc5f4,
      -0.81em 0.21em 0 -0.48em #5bc5f4;
    }
    55% {
      box-shadow:-0.11em -0.83em 0 -0.4em #eead0e,
      -0.29em -0.78em 0 -0.42em #66cd00,
      -0.43em -0.72em 0 -0.44em #2ab6f4,
      -0.52em -0.65em 0 -0.46em #2ab6f4,
      -0.57em -0.61em 0 -0.48em #2ab6f4;
    }
    100% {
      -webkit-transform:rotate(360deg);
      transform:rotate(360deg);
      box-shadow:-0.11em -0.83em 0 -0.4em #ee2c2c,
      -0.11em -0.83em 0 -0.42em #6495ed,
      -0.11em -0.83em 0 -0.44em #0fa4f6,
      -0.11em -0.83em 0 -0.46em #0fa4f6,
      -0.11em -0.83em 0 -0.48em #0fa4f6;
    }
  }

  @-webkit-keyframes loading {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
      box-shadow: -0.11em -0.83em 0 -0.4em #d4eaf4,
      -0.11em -0.83em 0 -0.42em #d4eaf4,
      -0.11em -0.83em 0 -0.44em #d4eaf4,
