      box-shadow:-0.11em -0.83em 0 -0.4em #a4dbf4,
      -0.11em -0.83em 0 -0.42em #a4dbf4,
      -0.11em -0.83em 0 -0.44em #a4dbf4,
      -0.11em -0.83em 0 -0.46em #a4dbf4,
      -0.11em -0.83em 0 -0.48em #a4dbf4;
    }
    30% {
      box-shadow:-0.11em -0.83em 0 -0.4em #5bc5f4,
      -0.51em -0.66em 0 -0.42em #5bc5f4,
      -0.75em -0.36em 0 -0.44em #5bc5f4,
      -0.83em -0.03em 0 -0.46em #5bc5f4,
      -0.81em 0.21em 0 -0.48em #5bc5f4;
    }
    55% {
      box-shadow:-0.11em -0.83em 0 -0.4em #2ab6f4,
      -0.29em -0.78em 0 -0.42em #2ab6f4,
      -0.43em -0.72em 0 -0.44em #2ab6f4,
      -0.52em -0.65em 0 -0.46em #2ab6f4,
      -0.57em -0.61em 0 -0.48em #2ab6f4;
    }
    100% {
      -webkit-transform:rotate(360deg);
      transform:rotate(360deg);
      box-shadow:-0.11em -0.83em 0 -0.4em #0fa4f6,
      -0.11em -0.83em 0 -0.42em #0fa4f6,
      -0.11em -0.83em 0 -0.44em #0fa4f6,
      -0.11em -0.83em 0 -0.46em #0fa4f6,
      -0.11em -0.83em 0 -0.48em #0fa4f6;
    }
  }

  /* load container */

  div.load-container {
    display: none;
    z-index: 9000;
    padding: 20px 0 20px 0;
    text-align: center;
  }
  div.load-container p {
    display: inline-block;
    position: relative;
    padding-left: 30px;
    font-size: 16px;
    font-weight: 700;
    color: #000;
  }
  div.load-container div.loader {
    display: inline-block;
    font-size: 30px;
    overflow: hidden;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    margin: auto;
    position: relative;
    animation: loading 1.5s infinite ease;
    -webkit-animation: loading 1.5s infinite ease;
  }

  #intro .researcher-head {
    border-bottom: 0;
    display: inline-block;
    margin: 0 0 1em 0;
    overflow: hidden;
    position: relative;
    width: 8em;
  }

  #intro .researcher-head:before {
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='200px' height='200px' viewBox='0 0 200 200' preserveAspectRatio='none' zoomAndPan='disable'%3E%3Cpolygon points='0,0 200,0 200,50 100,0 0,50' style='fill:%23f4f4f4' /%3E%3Cpolygon points='0,200 200,200 200,150 100,200 0,150' style='fill:%23f4f4f4' /%3E%3C/svg%3E");
    background-position: top left;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    content: '';
    display: block;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
  }

  #intro .researcher-head img {
    display: block;
    margin-left: -0.5em;
    width: 9em;
  }

  #intro header h2.this-researcher-name {
    font-size: 2em;
    font-weight: 900;
    text-align: left;
    text-decoration: none;
    font-family: "Raleway", Helvetica, sans-serif;
  }

  #intro header p.this-researcher-university {
    font-size: 1.2em;
    font-weight: 600;
    margin-bottom: 0.2em !important;
  }

  #intro header p.this-researcher-college {
    font-size: 0.8em;
    font-weight: 500;
  }

  @media screen and (max-width: 1280px) {

    #intro {
      margin: 0 0 3em 0;
      text-align: center;
    }

    #intro header h2.this-researcher-name {
      font-size: 2em;
      font-weight: 900;
      text-align: center;
      text-decoration: none;
      font-family: "Raleway", Helvetica, sans-serif;
    }

    #intro header p {
      font-size: 0.7em;
    }

  }

  @media screen and (max-width: 736px) {

    #intro {
      margin: 0 0 1.5em 0;
      padding: 1.25em 0;
    }

    #intro > :last-child {
      margin-bottom: 0;
    }

    #intro .logo {
      margin: 0 0 0.5em 0;
    }

    #intro header h2.this-researcher-name {
      font-size: 1.25em;
      font-weight: 900;
      text-align: center;
      text-decoration: none;
      font-family: "Raleway", Helvetica, sans-serif;
    }

    #intro header > :last-child {
      margin-bottom: 0;
    }

  }

  /* Footer */

  #footer .icons {
    color: #aaaaaa;
  }

  #footer .copyright {
    color: #aaaaaa;
    font-family: "Raleway", Helvetica, sans-serif;
    font-size: 0.5em;
    font-weight: 400;
    letter-spacing: 0.25em;
    text-transform: uppercase;
  }

  body.single #footer {
    text-align: center;
  }

  div#detail div.relationship-floating-div {
    display: block;
    height: fit-content;
    width: fit-content;
    max-width: 20em;
    max-height: 50em;
    background-color: #fafafa;
    overflow: scroll;
    border: 1px solid transparent;
  }

  div#detail div.relationship-floating-div p {
    margin: 0;
    padding: 0;
    color: #4f94cd;
  }

  div.opinion-div h3 {
    margin: 0 0 0.2em 0;
    padding: 0;
    font-size: 1em;
  }

  div.opinion-div p {
    margin: 0;
    padding: 0;
    font-size: 0.8em;
  }
</style>



