                  window.location.href = '/'
                  return
                } else {
                  if (testData.ccn_experts.length <= 0) {
                    window.location.href = '/'
                    return
                  }
                }
              } else {
                window.location.href = '/'
                return
              }
            } else {
              window.location.href = '/'
              return
            }
          } else {
            window.location.href = '/'
            return
          }
        }

        // 用弹栈数据来更新当前 Vue Data
        _this.indexPageData = event.state.indexPageData
        _this.expertQueryList = event.state.expertQueryList
        _this.queryCondition = event.state.queryCondition
        _this.expertQueryListPageTotal = event.state.expertQueryListPageTotal
        _this.expertQueryListPageCurrent = event.state.expertQueryListPageCurrent
        _this.showMenu = false

        // 挂载 HTML
        _this.mountIndex()
        // 初始化
        _this.initIndex()
        // 使用数据渲染页面
        _this.renderIndexPage(event.state.indexPageData)
        // 渲染搜索结果
        _this.queryListAjax(event.state.queryCondition, event.state.queryCondition.current_page)
        // history.replaceState(null, document.title, location.href)
      } else {
        // 若回退/前进的页面是 Detail Page，而不再是 Index Page，则重载首页
        // window.location.href = '/'
        console.log('Not Index Page')
      }
    })

    // 挂载 HTML
    _this.mountIndex()
    // AJAX 获得首页数据
    _this.loadHomePage()
    // 初始化
    _this.initIndex()
