    // 压入历史栈
    pushState (query) {
      let _this = this
      let popStateData = {}

      popStateData.indexPageData = _this.indexPageData // 进入首页时从后台得到的数据
      popStateData.expertQueryList = _this.expertQueryList // 查询专家时从后台得到的数据
      popStateData.queryCondition = _this.queryCondition // 记录当前的查询条件
      popStateData.expertQueryListPageTotal = _this.expertQueryListPageTotal // 查询专家结果-总页数
      popStateData.expertQueryListPageCurrent = _this.expertQueryListPageCurrent // 查询专家结果-当前页码
      popStateData.activeHottestExpert = _this.activeHottestExpert // 激活态的热门专家类别
      popStateData.showMenu = _this.showMenu // 是否展示菜单栏
      console.log(popStateData)

      if (location.href.indexOf('#') !== -1) {
        console.log('压入历史栈: ' + location.href.split('#')[0] + '#/' + query)
        history.pushState(popStateData, document.title, location.href.split('#')[0] + '#/' + query)
      } else {
        console.log('压入历史栈: ' + location.href)
        history.pushState(popStateData, document.title, location.href)
      }
    },
