      popStateData.expertQueryListPageCurrent = _this.expertQueryListPageCurrent // 查询专家结果-当前页码
      popStateData.activeHottestExpert = _this.activeHottestExpert // 激活态的热门专家类别
      popStateData.showMenu = _this.showMenu // 是否展示菜单栏
      console.log(popStateData)

      if (location.href.indexOf('#') !== -1) {
        console.log('修改历史栈栈顶: ' + location.href.split('#')[0] + '#/' + query)
        history.replaceState(popStateData, document.title, location.href.split('#')[0] + '#/' + query)
