          event.preventDefault()

          // Not visible?
          if (!jquerySearch.hasClass('visible')) {
            // Reset form.
            jquerySearch[0].reset()

            // Show.
            jquerySearch.addClass('visible')

            // Focus input.
            jquerySearchInput.focus()
          }
        })

      jquerySearchInput
        .on('keydown', function (event) {
          if (event.keyCode === 27) {
            jquerySearchInput.blur()
          }
        })
        .on('blur', function () {
          window.setTimeout(function () {
            jquerySearch.removeClass('visible')
          }, 100)
        })

      // Intro.
      let intro = $('#intro')

      // Move to main on <=large, back to sidebar on >large.
      breakpoints.on('<=large', function () {
        intro.prependTo(jqueryMain)
      })

      breakpoints.on('>large', function () {
        intro.prependTo(jquerySidebar)
      })

      // 向服务器请求 session，判断用户是否已经登录
      let jqUsername = $('input.username')
      let jqPassword = $('input.password')
      let jqLogin = $('section.menu a.login')
      let jqRegister = $('section.menu a.register')

      let serverUrl = 'check_login'
      let data = {}

      // HTTP Request (JQuery Ajax)
      $.ajax({
        type: 'GET',
        url: serverUrl,
        data: data,
        dataType: 'json',
        beforeSend: function (xhr) {
        },
        success: function (result, status, xhr) {
          console.log(result)
          if (!result || result === '' || result.length === 0) {
            alert('抱歉，登录失败！')
            console.log('HTTP result 异常！')
            return false
          } else {
            // 解析JSON数据
            let checkLoginResult = result.checkLoginResult

            // 验证从后端返回的登录结果标志
            console.log('checkLoginResult === ' + checkLoginResult)
            if (checkLoginResult === 'success') {
              console.log('根据 Session 记录，用户已经登录。')

              let userId = result.userId
              if (userId !== null && userId !== undefined) {
                // 存储 userId 到 sessionStorage
                sessionStorage.setItem('user_id', userId)

                // 设置 UI
                jqUsername.hide()
                jqPassword.hide()
                jqLogin.text('注销')

                // 让注册按钮不可选
                if (!jqRegister.hasClass('disabled')) {
                  jqRegister.addClass('disabled')
                }

                // 修改 VUEX 里的登录状态
                if (!_this.$store.state.loginFlag) {
                  _this.$store.dispatch('changeLoginFlag', true)
                }
              }
            } else if (checkLoginResult === 'fail') {
              console.log('根据 Session 记录，用户尚未登录。')
            } else {
              console.log('查询 Session 记录，后端返回数据异常。')
            }
          }
        },
        error: function (xhr, status, error) {
          alert('HTTP GET Error! status=' + status + '&error=' + error +
            '&statusCode=' + xhr.status + '&responseText=' + xhr.responseText +
            '&readyState=' + xhr.readyState)
        },
        statusCode: {
          // 当响应对应的状态码时，执行对应的回调函数
          200: function () {
            console.log('200: 请求成功')
          },
          404: function () {
            console.log('404: 找不到页面')
            alert('404: 找不到页面')
          },
          500: function () {
            console.log('500: 服务器错误')
            alert('500: 服务器错误')
          }
        }
      })
    }
  }
}
</script>

