                // 解析JSON数据
                let logoutResult = result.logoutResult

                // 验证从后端返回的登录结果标志
                console.log('logoutResult === ' + logoutResult.toString())
                if (logoutResult === 'success') {
                  console.log('注销成功！')
                  jqUsername.show()
                  jqPassword.show()
                  jqLogin.text('登录')

                  // 让注册按钮可选
                  if (jqRegister.hasClass('disabled')) {
                    jqRegister.removeClass('disabled')
                  }

                  // 修改登录状态
                  if (_this.$store.state.loginFlag) {
                    _this.$store.dispatch('changeLoginFlag', false)
                  }

                  // 压入历史栈
                  // _this.pushState('')

                  // alert('注销成功！')
                } else if (logoutResult === 'fail') {
                  console.log('抱歉，注销失败！')
                  alert('抱歉，注销失败！')
                } else {
                  console.log('抱歉，注销失败！')
                  alert('异常，注销失败！')
                }
              }
            },
            error: function (xhr, status, error) {
              alert('HTTP GET Error! status=' + status + '&error=' + error +
                '&statusCode=' + xhr.status + '&responseText=' + xhr.responseText +
                '&readyState=' + xhr.readyState)
            },
            statusCode: {
              // 当响应对应的状态码时，执行对应的回调函数
              200: function () {
                console.log('200: 请求成功')
              },
              404: function () {
                console.log('404: 找不到页面')
                alert('404: 找不到页面')
              },
              500: function () {
                console.log('500: 服务器错误')
                alert('500: 服务器错误')
              }
            }
          })
        }
      } else if (choice === '登录') {
        if (!username || username === '') {
          console.log('请您输入账号！')
          jqUsername.focus()
        } else if (!password || password === '') {
          console.log('请您输入密码！')
          jqPassword.focus()
        } else {
          let serverUrl = 'login'
          let postData = {}
          postData.username = username
          postData.password = password

          // HTTP Request (JQuery Ajax)
          $.ajax({
            type: 'GET',
            url: serverUrl,
            data: postData,
            dataType: 'json',
            beforeSend: function (xhr) {
            },
            success: function (result, status, xhr) {
              console.log(result)
              if (!result || result === '' || result.length === 0) {
                alert('抱歉，登录失败！')
                console.log('HTTP result 异常！')
                return false
              } else {
                // 解析JSON数据
                let loginResult = result.loginResult

                let flag = true

                // 验证从后端返回的登录结果标志
                console.log('loginResult === ' + loginResult.toString())
                if (loginResult !== 'success') {
                  console.log('登录验证失败')
                  flag = false
                }

                // 如果没有应有的数据，则不会跳转到用户中心页面
                // let testData = userInfo
                // if (JSON.stringify(testData) !== '{}') {
                //   if (testData !== undefined) {
                //     if ('id' in testData) {
                //       if (testData.id === undefined) {
                //         flag = false
                //       }
                //     } else {
                //       flag = false
                //     }
                //   } else {
                //     flag = false
                //   }
                // } else {
                //   flag = false
                // }

                if (flag) {
                  console.log('登录成功！')
                  jqUsername.hide()
                  jqPassword.hide()
                  jqLogin.text('注销')

                  // 让注册按钮不可选
                  if (!jqRegister.hasClass('disabled')) {
                    jqRegister.addClass('disabled')
                  }

                  // 修改 VUEX 里的登录状态
                  if (!_this.$store.state.loginFlag) {
                    _this.$store.dispatch('changeLoginFlag', true)
                  }

                  // 压入历史栈
                  // _this.pushState('')

                  // 路由带参跳转
                  // let userData = {}
                  // userData.userInfo = userInfo
                  // userData.favoriteExpertList = favoriteExpertList
                  // console.log(userData)
                  //
                  // _this.$router.push({
                  //   name: 'UserCenter', params: {userData: userData}
                  // })
                  alert('登录成功！')
                } else {
                  console.log('登录失败！')
                  alert('抱歉，登录失败！')
                  return false
                }
              }
            },
            error: function (xhr, status, error) {
              alert('HTTP GET Error! status=' + status + '&error=' + error +
                '&statusCode=' + xhr.status + '&responseText=' + xhr.responseText +
                '&readyState=' + xhr.readyState)
            },
            statusCode: {
              // 当响应对应的状态码时，执行对应的回调函数
              200: function () {
                console.log('200: 请求成功')
              },
              404: function () {
                console.log('404: 找不到页面')
                alert('404: 找不到页面')
              },
              500: function () {
                console.log('500: 服务器错误')
                alert('500: 服务器错误')
              }
            }
          })
        }
      } else {
        console.error('choice error !!!')
      }
    },
    // TODO 用户注册
    register () {
      'use strict'

      let _this = this
      let jqUsername = $('input.username')
      let jqPassword = $('input.password')
      let jqLogin = $('section.menu a.login')
      let jqRegister = $('section.menu a.register')

      let username = jqUsername.val().trim()
      let password = jqPassword.val().trim()

      if (!username || username === '') {
        console.log('请您输入账号！')
        jqUsername.focus()
      } else if (!password || password === '') {
        console.log('请您输入密码！')
        jqPassword.focus()
      } else {
        // 做基本的输入格式判断，后端要做更细致的判断
        let usernamePattern = /^[a-zA-Z_][0-9a-zA-Z_]+$/ // 由数字、英文字母和下划线组成，且不能以数字开头
        let passwordPattern = /^[\S]+$/ // \S 匹配任何非空白字符, 等价于 [^\f\n\r\t\v]
        if (username.length < 6) {
          console.log('用户名至少应有6位字符！')
          jqUsername.focus()
        } else if (username.length > 16) {
          console.log('用户名不应超过16位字符！')
          jqUsername.focus()
        } else if (password.length < 8) {
          console.log('密码至少应有8位字符！')
          jqPassword.focus()
        } else if (password.length > 18) {
          console.log('密码不应超过18位字符！')
          jqUsername.focus()
        } else if (password.indexOf(username) !== -1) {
          console.log('密码中不应包含您的用户名！')
          jqUsername.focus()
        } else if (!usernamePattern.test(username)) {
          console.log('用户名只能由数字、英文字母和下划线组成，且不能以数字开头！')
          jqUsername.focus()
        } else if (!passwordPattern.test(password)) {
          console.log('密码中不能包含空白字符！')
          jqPassword.focus()
        } else {
          let serverUrl = 'register'
          let postData = {}
          postData.username = username
          postData.password = password

          // jqUsername.hide()
          // jqPassword.hide()
          // jqLogin.text('注销')
          // // 让注册按钮不可选
          // if (!jqRegister.hasClass('disabled')) {
          //   jqRegister.addClass('disabled')
          // }
          // // 修改登录状态
          // if (!_this.$store.state.loginFlag) {
          //   _this.$store.dispatch('changeLoginFlag', true)
          // }
          //
          // // 切换路由
          // this.$router.replace('/register/' + expertId)

          // HTTP (JQuery Ajax)
          $.ajax({
            type: 'POST',
            url: serverUrl,
            data: postData,
            // crossDomain: true,
            // dataType: "jsonp",
            // jsonp: "jsonp_callback",
            // jsonpCallback: "jsonpCallbackFunction",
            dataType: 'json',
            // async: true, // 使用同步操作
            // timeout: 20000, // 超时时间：20秒
            beforeSend: function (xhr) {
            },
            success: function (result, status, xhr) {
              // alert('HTTP Success! normal_query: ' + 'result=' + result);

              if (!result || result === '' || result.length === 0) {
                console.log(result)
                console.log('注册失败！')
                return false
              } else {
                console.log(result)
              }
              jqUsername.hide()
              jqPassword.hide()
              jqLogin.text('注销')
              // 让注册按钮不可选
              if (!jqRegister.hasClass('disabled')) {
                jqRegister.addClass('disabled')
              }
              // 修改登录状态
              if (!_this.$store.state.loginFlag) {
                _this.$store.dispatch('changeLoginFlag', true)
              }
              console.log('注册成功！')

              // 压入历史栈
              _this.pushState('paper_detail/' + _this.paperInfo.paper_id)
            },
            error: function (xhr, status, error) {
              alert('HTTP Error! status=' + status + '&error=' + error +
                '&statusCode=' + xhr.status + '&responseText=' + xhr.responseText +
                '&readyState=' + xhr.readyState)
            },
            statusCode: {
              // 当响应对应的状态码时，执行对应的回调函数
              200: function () {
                console.log('200: 请求成功')
              },
              404: function () {
                console.log('404: 找不到页面')
                alert('404: 找不到页面')
              },
              500: function () {
                console.log('500: 服务器错误')
                alert('500: 服务器错误')
              }
            }
          })
        }
      }
    },
    // TODO 跳转到用户中心页面
    jumpUserCenter () {
      'use strict'

      let _this = this
      // let jqUsername = $('input.username')
      let loginFlag = _this.$store.state.loginFlag
      console.log(loginFlag)

      if (!loginFlag) {
        console.log('尚未登录！')
        alert('您尚未登录，因此无法进入用户中心。请在菜单栏里进行登录操作。')
        // 打开菜单栏，光标聚焦于登录输入框
        _this.showMenu = false
        _this.toggleShowMenu()
        $('input.username').focus()
      } else {
        let serverUrl = 'user_center'
        let data = {}

        // HTTP Request (JQuery Ajax)
        $.ajax({
          type: 'GET',
          url: serverUrl,
          data: data,
          dataType: 'json',
          beforeSend: function (xhr) {
          },
          success: function (result, status, xhr) {
            console.log(result)
            if (!result || result === '' || result.length === 0) {
              alert('抱歉，无用户信息，无法跳转用户中心。')
              console.log('抱歉，无用户信息，无法跳转用户中心。')
              return false
            } else {
              // 解析JSON数据
              let loginResult = result.loginResult

              // 验证从后端返回的登录结果标志
              console.log('loginResult === ' + loginResult.toString())
              if (loginResult !== 'success') {
                console.log('抱歉，登录验证失败，无法跳转用户中心。')
                alert('抱歉，登录验证失败，无法跳转用户中心。')
                return false
              } else {
                let flag = true

                let userInfo
                if ('myuser' in result) {
                  userInfo = JSON.parse(result.myuser)
                } else {
                  flag = false
                }
                let favoriteExpertList = []

                if ('favExperts' in result) {
                  let favoriteExpertListTemp = JSON.parse(result.favExperts)
                  favoriteExpertListTemp.forEach(function (item, index) {
                    favoriteExpertList.push(JSON.parse(item))
                  })
                } else {
                  flag = false
                }

                // 如果没有应有的数据，则不会跳转到用户中心页面
                let testData = userInfo
                if (JSON.stringify(testData) !== '{}') {
                  if (testData !== undefined) {
                    if ('id' in testData) {
                      if (testData.id === undefined) {
                        flag = false
                      }
                    } else {
                      flag = false
                    }
                  } else {
                    flag = false
                  }
                } else {
                  flag = false
                }

                if (flag) {
                  console.log('登录验证成功！跳转用户中心。')

                  // 压入历史栈
                  _this.pushState('paper_detail/' + _this.paperInfo.paper_id)

                  // 路由带参跳转
                  let userData = {}
                  userData.userInfo = userInfo
                  userData.favoriteExpertList = favoriteExpertList
                  console.log(userData)

                  // _this.$router.push({
                  //   name: 'UserCenter', params: {userData: userData}
                  // })
                  let path = '/user_center/' + userInfo.id
                  _this.$router.push({path: path, query: {userData: userData}})
                } else {
                  console.log('抱歉，无用户信息，无法跳转用户中心。')
                  alert('抱歉，无用户信息，无法跳转用户中心。')
                  return false
                }
              }
            }
          },
          error: function (xhr, status, error) {
            alert('HTTP GET Error! status=' + status + '&error=' + error +
              '&statusCode=' + xhr.status + '&responseText=' + xhr.responseText +
              '&readyState=' + xhr.readyState)
          },
          statusCode: {
            // 当响应对应的状态码时，执行对应的回调函数
            200: function () {
              console.log('200: 请求成功')
            },
            404: function () {
              console.log('404: 找不到页面')
              alert('404: 找不到页面')
            },
            500: function () {
              console.log('500: 服务器错误')
              alert('500: 服务器错误')
            }
          }
        })
      }
    },
    // 切换显示菜单栏
    toggleShowMenu () {
      // console.log('toggleShowMenu () ', this.showMenu)
      this.showMenu = !this.showMenu

      // let _this = this
      let jqMainModule = $('article.main-module')
      if (this.showMenu) {
        jqMainModule
          .css({'opacity': '0.4'})
        // .unbind('click').click(_this.toggleShowMenu) // 点击主题区域则退出菜单栏

        // 显示菜单栏
        $('section.menu').css({'display': 'block'})
      } else {
        jqMainModule
          .css({'opacity': '1'})
        // .unbind('click').click(function () { // 停止事件响应
        //   event.stopPropagation()
        // })

        // 隐藏菜单栏
        $('section.menu').css({'display': 'none'})
      }
    }
  }
}
</script>

